  public static bmgfGetAll(){
    let SqlQuery = "Select FM.* from (Select FormulaID,Description,RevisionNumber,RevisionDate,FormulaClassID,Fmstate as RevisionStatus,StatusID,RecOwner,RecUserId,RecDate   FROM FMMast      Union All Select FormulaID,Description,RevisionNumber,RevisionDate,FormulaClassID,Fmstate as RevisionStatus,StatusID,RecOwner,RecUserId,RecDate   FROM FMrevhs) FM ,     (Select case data  when 'A' then 'Active' when 'D' then 'DEVELOPMENT' when 'C' then 'CANCELLED'   when 'AP' then  'APPROVED'  when 'O' then 'Obsolete' when 'P' then 'Pending' when 'H' then     'HOLD'  end data   from dbo.BMEOnlineFunctSplit ((select isnull(NavigationSetup,'A,D')  from CSOne),',')) as X  where  FM.RevisionStatus Not in ('Obsolete') and  CHARINDEX(StatusID,     (SELECT StatusNAinCA FROM CSOne)) =0  AND      (  (isnull((SELECT  recordlevelsecurity FROM FMDefaults),'NO') ='NO' ) or  ( (SELECT  recordlevelsecurity FROM FMDefaults) ='YES' ) )  and   ((FM.RevisionStatus =isnull(X.data, 'Active'))      or (FM.RevisionStatus =isnull(X.data, 'DEVELOPMENT')) or (FM.RevisionStatus =isnull(X.data, 'Pending')) or  ( FM.RevisionStatus =isnull(X.data, 'HOLD')) or  (FM.RevisionStatus =isnull(X.data,      'CANCELLED'))  or  (FM.RevisionStatus =isnull(X.data, 'APPROVED'))) Order by FormulaID,RevisionNumber "
    return SqlQuery
  }






