  bmlfonProcess() {
    debugger
    // Check permission and return
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (this.mObjModel.FormulaKeyFrom > this.mObjModel.FormulaKeyTo) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('FORMULALOSSADJUSTMENT.FormulaKeyLessthan')));
      return false;      
    }

    if(this.mObjModel.Fromdate==null){
      this.mObjModel.Fromdate = new Date('1900/01/01');
    }
    if(this.mObjModel.Todate==null){
      this.mObjModel.Todate = new Date('3001/01/01');
    }

    if(this.mObjModel.UpdateLoss){
      this.mbShowDialogTrial = true;      
    }else{
      this.mbShowDialogTrial = true;
    }

    
    

  }

