bmlfonDialogTrial(action) {
  
  if (action == 'Cancel') {
      this.mbShowDialogTrial = false;
  }
  else if (action == 'Ok') {
    this.mbShowDialogTrial = false;
    this.bmlfonprocessaftertrialandfinal()     
  }
}

bmlfonprocessaftertrialandfinal(){
  let InputData = [];
  //InputData.push(this.mObjModel);
  InputData.push({
      FormulaKeyFrom: this.mObjModel.FormulaKeyFrom,
      FormulaKeyTo: this.mObjModel.FormulaKeyTo,
      Fromdate: this.mObjModel.Fromdate,
      Todate: this.mObjModel.Todate,
      UpdateLoss: this.mObjModel.UpdateLoss
  });

  //console.log(InputData)
  //console.log(this.mObjModel)
  let reqData: any = {
    JSON: JSON.stringify({
      HeaderData: InputData,
      Response: [{
        "ResponseText": "", "ErrorCode": "","Pdfpath":""
      }]
    })
  };
  this.mbloaderEnabled = true;
  this.objService.bmlfonProcessData(reqData).subscribe(res => {
    debugger
    if (res == null) {
      
      this.objToastService.notifyError('Server Error');
      this.mbloaderEnabled = false;
      return false;
    }
    if (res.Response[0].ErrorCode == "") {
      //this.objToastService.notifySuccess(res.Response[0].ResponseText);
      this.mbloaderEnabled = false;
      this.pdfSrc = res.Response[0].Pdfpath
      this.modalservice.open('pdfViewerModal');
      // this.msReport = res.Response[0].ResponseText;
      // this.bmlfReportModal('open');
      return false;
    }
    else {
      if(res.Response[0].ResponseText ==''){
        this.objToastService.notifyError('Error in service.')
      }
      else
      {
        this.objToastService.notifyError(res.Response[0].ResponseText)
      }
      this.mbloaderEnabled = false;
    }

    console.log(this.gridSettings)
    this.mbloaderEnabled = false;
  }); 
}

//Open Task MODAL 
bmlfOpenTaskModal(taskType) {
  this.mobjTaskData = {};
  this.mobjTaskData.taskType = taskType;
  if (taskType == 'myTask') {
    this.mobjTaskData.title = this.objUtils.bmgfonTranslate('COMMON.MYTASK');
    this.mobjTaskData.innerRecord = this.mobjUserData;
  } else if (taskType == 'recordTask') {
    this.mobjTaskData.title = this.objUtils.bmgfonTranslate('COMMON.TASKFORRECORD');
    let pobjFormRec: any = {};
    pobjFormRec.formName = this.msScreenname;
    pobjFormRec.primeKey = "" //this.mObjModel.Fromdate + '|' + this.mObjModel.Todate;
    this.mobjTaskData.innerRecord = pobjFormRec;
  }
  this.mbIsTaskModal = true;
}

