bmlfononParameterSave() {
  debugger
  let paramValue = this.parameterValue; //this.paramCtrl.nativeElement.value;
  let newObj = this.mObjModel;

  if (paramValue == '') {
    this.onOpenErrorBlock(this.objUtils.bmgfonTranslate('FORMULALOSSADJUSTMENT.PRMEMPTY'));
    return;
  }
  else {
    
    this.mbloaderEnabled = true;
    this.msscreenModeUpdate = "I";
    let jObj = {};
    let headerData = [];
    let jArray = [];
    let counterRecId = 0;
    //let oArray = [...this.dataListCtrl.nativeElement.options];
    jObj["ParamName"] = paramValue;
    jObj["ScreenName"] = this.msScreenname;
    // oArray.some(res => {
    //   if (res.value == paramValue) {
    //     this.msscreenModeUpdate = 'U';
    //     return true
    //   }
    // })

    if(this.paramValueArray.length>0){
      let list = this.paramValueArray.filter(x => x.PARANAME === paramValue)[0];
      if(list !=undefined){
        this.msscreenModeUpdate = 'U';
        jObj["UpdateMode"] = this.msscreenModeUpdate;
      }
      else{
        jObj["UpdateMode"] = this.msscreenModeUpdate;
      }
    }
    else
    {
      jObj["UpdateMode"] = this.msscreenModeUpdate;
    }

    headerData.push(jObj);
    Object.keys(newObj).map(key => {
      if (typeof (newObj[key]) == "string") {
        let tempJObj = {};
        let nTempJObj = {}
        tempJObj["ControlName"] = RptParameterMap[key]
        tempJObj["ControlValue"] = newObj[key]
        tempJObj["RecId"] = counterRecId++;
        jArray.push(tempJObj);
      }
      else if (typeof (newObj[key]) == "boolean") {
        debugger
        let checkJobj = {};
        checkJobj["ControlName"] = RptParameterMap[key];
        checkJobj["ControlValue"] = newObj[key] == true ? 1 : 0;
        checkJobj["RecId"] = counterRecId++;
        jArray.push(checkJobj)
      }
      else if (typeof (newObj[key]) == "object") {
        let dateJobj = {};
        dateJobj["ControlName"] = RptParameterMap[key];
        dateJobj["ControlValue"] = moment(newObj[key]).format('DD/MMM/YYYY');
        dateJobj["RecId"] = counterRecId++;
        jArray.push(dateJobj)

      }
      else {
      }
    });
    this.objService.saveReportParameters(jArray, headerData).subscribe(res => {
      if (res != null) {
        if (res['Response'][0].ResponseText == "Sucess") {
          //this.paramCtrl.nativeElement.value = "";
          this.parameterValue = '';
          this.mbloaderEnabled = false;
          this.getParam(this.msScreenname);
        }
        else {
          console.trace();
          console.error('Save error');
          this.mbloaderEnabled = false;
        }
      }
    })
  }

}

private fetchParamData(param) {
  let pValue = param;
  this.mbloaderEnabled = true;
  this.objService.getReportParameterValue(this.msScreenname, param).subscribe(res => {
    if (res != null && res.RPTPARAMETERSBYVALUE.length > 0) {
      this.bmlfclearModel();
      this.fillControlFromParam(res.RPTPARAMETERSBYVALUE);
    }
    else {
      console.error('No Response');
      console.trace();
      this.mbloaderEnabled = false;
    }
  })
}

private fillControlFromParam(resData) {
  
  resData.forEach(element => {
    if (element.CONTROLNAME == "PVDate22(1)" || element.CONTROLNAME == "PVDate22(0)" || element.CONTROLNAME == "Dates") {
      let key = this.bmlfongetKeyFromValue(element.CONTROLNAME);
      this.mObjModel[key] = new Date(element.CONTROLVALUE);
    }
    else if ((element.CONTROLNAME == "ChktoShowReport" || element.CONTROLNAME == "chkPrintComment" || element.CONTROLNAME == "ChkLots" || element.CONTROLNAME == "chkRepWithoutLabels")) {
      let key = this.bmlfongetKeyFromValue(element.CONTROLNAME);
      this.mObjModel[key] = element.CONTROLVALUE == 0 ? false : true;
    }
    else {
      let key = this.bmlfongetKeyFromValue(element.CONTROLNAME);
      this.mObjModel[key] = element.CONTROLVALUE;

    }
  });
  this.mbloaderEnabled = false;
}

bmlfongetKeyFromValue(value) {
  let newObj = this.mObjModel;
  let tempKey = Object.keys(newObj).find(data => {
    return RptParameterMap[data] == value
  });
  return tempKey;
}

