import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
  providedIn: 'root'
})
export class CostingSetupService {

  constructor(private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService) { }

  public BMGFGetCostingSetup(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetCostingSetup`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  public BMGFSaveCostingSetup(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveCostingSetup`;
    return this.httpExt.post<any>(url, headerData, null)
  }
}
