import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class FormulaLossAdjustmentService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  public bmlfonProcessData(headerData: any): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFProcessFormulaLossAdjustment`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  getReportParameter(screenName) {
    let json = { 'ScreenName': screenName }
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReportParameter`;
    return this.httpExt.post(url, json, null);

  }
  getReportParameterValue(screenName, param) {
    let json = { 'ScreenName': screenName, 'ParametreValue': param }
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReportParameterValue`;
    return this.httpExt.post(url, json, null);

  }
  saveReportParameters(controls, header) {
    let oData: any = {};
    oData.HeaderData = [...header];
    oData.Controls = [...controls];
    oData.Response = [];
    oData.Response.push({ "ResponseText": "", "ErrorCode": "" });
    let jData = JSON.stringify(oData);
    let tempjData = JSON.stringify(jData);
    let json = '{"CustomToken":' + tempjData + '}';
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveCustomParameter`;
    return this.httpExt.post(url, json, null);
  }
  DeleteReportParameter(screenname, ParaValue) {
    let json = { 'ScreenName': screenname, 'ParametreValue': ParaValue }
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteCustomParameters`
    return this.httpExt.post(url, json, null)

  }
}
