  bmlfonLoadInitial() {
    this.mskeyValue = this.activatedRoute.snapshot.queryParamMap.get('FormulaID');
    this.bmlfGetDrillDownList();
  }


  //#region  Hyperlink GetDrillDownList
  bmlfGetDrillDownList() {
    ////
    let pobjReqData = {
      ScreenName: 'ApplyPaymentComponent',
      LookUpKey: 'DrillDwnList',
      Filter1: 'Product Cost Analysis'
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).pipe(
      tap(() => this.mbIsLoading = true), delay(2000)).subscribe(res => {
        ////;
        this.mbIsLoading = false;
        if (res.LookupData.length > 0) {
          this.marrDrillDownList = res.LookupData;
          this.mbIsHyerLinkRender = true;
          this.mbShowGridHyperlinks = true;
        }
        //this.appUtility.getBMEScreePermission(LookupKey.ScreenName); // 03/dec/2020 for screen permission
        this.msCostMethod = this.appUtility.bmlfGetCostMethod();

        let pobjReqData = {
          ScreenName: 'ProductCostAnalysisConst',
          LookUpKey: 'DecimalPrecision'
        }
        this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
          if (res.LookupData.length > 0) {
            this.msDefaultDecimal = res.LookupData[0].Default_Value;
          }
        });
        let pobjReqData1 = {
          ScreenName: 'ProductCostAnalysisConst',
          LookUpKey: 'WtVolUOM'
        }
        this.userFieldService.GETGeneralSqlData(pobjReqData1).subscribe(res => {
          if (res.LookupData.length > 0) {
            this.msWtUOM = res.LookupData[0].WtUOM;
            this.msVolUOM = res.LookupData[0].VolUOM
          }
        });
        let pobjReqData2 = {
          ScreenName: 'ProductCostAnalysisConst',
          LookUpKey: 'CostingSetupData'
        }
        this.userFieldService.GETGeneralSqlData(pobjReqData2).subscribe(res => {
          if (res.LookupData.length > 0) {
            this.mobjCostSetup = res.LookupData;
            if (this.mobjCostSetup != undefined) {
              if (this.mobjCostSetup[0]['DfltFinishedGoodsCostMethod'] == '1') {
                this.mbIsMarkup = true;
                this.mbIsMargin = false;
              } else {
                this.mbIsMarkup = false;
                this.mbIsMargin = true;
              }
            }
          }
        });
        // let pobjReqData12 = {
        //   SqlQuery: SQL.MQuery
        // }
        // this.userFieldService.getCustomSqlData(pobjReqData12).subscribe(res => {
        //   if (res.LookupData.length > 0) {
        //     this.msDefaultDecimal = res.LookupData[0].Default_Value;
        //     this.msWtUOM = res.Table1[0].WtUOM;
        //     this.msVolUOM = res.Table1[0].VolUOM
        //     this.mobjCostSetup = res.Table2;


        //   }
        // });

        this.msFormulaID = this.activatedRoute.snapshot.queryParamMap.get('FormulaID');
        this.mObjfmast.FormulaID = this.msFormulaID;
        this.msRevisionNumber = this.activatedRoute.snapshot.queryParamMap.get('RevisionNumber');
        this.msCopy = this.activatedRoute.snapshot.queryParamMap.get('COPY');
        if (!this.isEmpty(this.mObjfmast.FormulaID)) {
          this.bmlfGetCostAnalysisDetailByID();
        } else {
          //this.msRevisionDate = moment(new Date()).format('MM/DD/YYYY  h:mm:ss a');
          let dtformat = this.msCustomDateFormat.toUpperCase() + ' h:mm:ss A';  //08/feb/2021 scr no.1149785
          this.msRevisionDate = moment(new Date()).format(dtformat);
          setTimeout(function () { document.getElementById('txtFormulaID').focus(); }, 10);
          this.msFormMode = 'I';
          this.mbCustomControl = true;
        }
        if (this.msCopy != 'YES') {
          this.bmlfGetFMDefaultValues();
        }

      });
  }
