  //#region Load Formula Entry Default
  bmlfLoadFMDefaults() {
    let reqData: any = {
      FMMast: [],
      FMVarCst: [],
      Response: [{ ResponseText: "", ErrorCode: "" }]
    };
    reqData.FMMast.push(this.mObjfmast1);
    reqData.FMVarCst.push(this.mObjfmvarCst);
    let pSrequestObj: any = {
      JSON: JSON.stringify(reqData)
    };
    this.formulaEntryService.BMGFLoadFMDefaults(pSrequestObj).subscribe(res => {
      if (this.isEmpty(res.Response[0].ErrorCode)) {
        if (!this.mbPKisDisable) {
          try {
            res.FMMast[0]['FixedCostHours'] = Number(res.FMMast[0]['FixedCostHours']);
            res.FMMast[0]['FixedLaborAmount'] = Number(res.FMMast[0]['FixedLaborAmount']);
            res.FMMast[0]['FixedOverheadFactor'] = Number(res.FMMast[0]['FixedOverheadFactor']);
            res.FMMast[0]['LossConstant'] = Number(res.FMMast[0]['LossConstant']);
            res.FMMast[0]['LossFactor'] = Number(res.FMMast[0]['LossFactor']);
            res.FMMast[0]['MarkupFactor'] = Number(res.FMMast[0]['MarkupFactor']);
            res.FMMast[0]['SetupCostHours'] = Number(res.FMMast[0]['SetupCostHours']);
            res.FMMast[0]['SetupLaborAmount'] = Number(res.FMMast[0]['SetupLaborAmount']);
            res.FMMast[0]['SetupOverheadFactor'] = Number(res.FMMast[0]['SetupOverheadFactor']);
            res.FMMast[0]['SpecGravityOverride'] = Number(res.FMMast[0]['SpecGravityOverride']);
          }
          catch (e) {
            console.log('value format error');
          }

          this.mObjfmast = Object.assign({}, res.FMMast[0]);
          if (this.isEmpty(this.mObjfmast.FormulaCostMethod)) {
            this.mObjfmast.FormulaCostMethod = this.msCostMethod;
          }
          this.mObjfmvarCst = Object.assign({}, res.FMVarCost[0]);
          this.marrLineGridData = res.FMVarCost;
          this.marrLineGridData.splice(0, 1)
        }
        this.formulaEntryService.BMGFGetFMOtherDefaults().subscribe(res => {
          this.marrComboValue = res.ComboValues;
          this.marrFMRevisionSetup = res.FMRevisionSetup;
          this.marrLabDefaultSetup = res.LabDefaultSetup;
          this.marrHMISdropdown = this.marrComboValue.filter(x => x.ValueID == 'HMISR');
        });
        // //  INTERMEDIATE DEFAULT
        // this.formulaEntryService.BMGFGetIMDefaults().subscribe(res => {
        //   this.marrCIDefaultDetails=res.Details;
        //   this.marrCIDefaultHeader=res.Header;
        //   console.log('INTERMEDIATE DEFAULT');
        //   console.log(res);
        //   res.Header.forEach((element) => {
        //     if (element.MfieldName == 'Item Location') {
        //       this.mObjCItermediate.ItemLocation = element.MfieldValue;
        //     }
        //   })
        // });
        // //CREATE FINISHED GOODS
        // this.formulaEntryService.BMGFGetFGDefaults().subscribe(res => {
        //   this.marrFGDefaultDetails=res.Details;
        //   this.marrFGDefaultHeader=res.Header;
        //   console.log('CREATE FINISHED GOODS');
        //   console.log(res);
        //   res.Header.forEach((element) => {
        //     if (element.MfieldName == 'Item Location') {
        //       this.mObjCFGoods.ItemLocation = element.MfieldValue;
        //       console.log(this.mObjCFGoods.ItemLocation);
        //     }
        //     if (element.MfieldName == 'Check Zero Cost' && element.MfieldValue=='N') {
        //       this.mObjCFGoods.CheckZeroCost = false;
        //     }
        //     if (element.MfieldName == 'Run Cost Rollup' && element.MfieldValue=='N') {
        //       this.mObjCFGoods.CostRollup = false;
        //     }
        //     if (element.MfieldName == 'Muliple BINs required' && element.MfieldValue=='N') {
        //       this.mObjCFGoods.FGBom = false;
        //     }
        //   })
        // });
      }
      //28/sep/2020 scr no.144476
      if (this.marrLineGridData.length > 0) {
        this.marrLineGridData.forEach((element) => {
          element.OrgMaxwt = element.Maxwt;
        });
      }
      this.bmlfgetDescription();
    });
  }
