  // //on Action
  bmlfonAction(psAction: any) {
    this.msAction = psAction;
    // 03/Dec/2020 For permission realted issue
    if (psAction == ACTION.ADD) {
      if (!this.appUtility.mbWrite) {
        this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return false;
      };
      if (this.isEmpty(this.mObjfmast.FormulaID)) {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.FORMULAKEY'));
        return false;
      }
    }
    else if (psAction == ACTION.COPY) {
      if (!this.appUtility.mbWrite) {
        this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return false;
      };
    }
    // 03/Dec/2020 For permission realted issue
    if (this.mbIsChange) {
      this.mbsaveDialogView = true;
    } else {
      this.bmlfOthrAction(this.msAction);
    }
  }
  bmlfOthrAction(psAction) {
    ;
    if (psAction == ACTION.ADD) {
      this.bmlfonAdd();
    } else if (psAction == ACTION.COPY) {
      this.bmlfonCopy();
    } else if (psAction == ACTION.SEARCH) {
      this.bmlfonSearch();
    } else if (psAction == ACTION.CLOSE) {
      this.router.navigate([NAVIGATE_URL.EDIT]);
    }
  }

  //  CLOSE SCREEN
  bmlfonCloseScreen() {
    //this.router.navigate([NAVIGATE_URL.EDIT]);
    this.bmlfonAction('CLOSE')
  }

