  // Get Formula By ID GetDataOnLoad
  async bmlfGetCostAnalysisDetailByID() {
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ FormulaID: this.msFormulaID, RevisionNumber: this.msRevisionNumber }],
        Response: [{ ResponseText: "", ErrorCode: "" }]

      })
    };

    this.productCostAnalysisService.BMGFGetCostAnalysisDetail(reqData).subscribe(async res => {
      if (this.isEmpty(res.Response[0].ErrorCode)) {
        this.mObjfmast = Object.assign({}, res.FMMast[0]);
        // 16s/sep/2020 scr no.144373
        if (this.msCopy != undefined) {
          if (this.msCopy == 'YES') {
            this.mObjfmast.recowner = '';
            this.mObjfmast.user_desc = '';
          }
        }
        this.mbPKisDisable = true;
        this.msFormMode = 'U';
        this.mbCustomControl = true;
        //this.msRevisionDate = moment(this.mObjfmast.RevisionDate).format('MM/DD/YYYY  h:mm:ss a')
        let dtformat = this.msCustomDateFormat.toUpperCase() + ' h:mm:ss A';  //08/feb/2021 scr no.1149785
        this.msRevisionDate = moment(this.mObjfmast.RevisionDate).format(dtformat);
        // 17/Sep/2020 SCR No.145200
        try {
          if (this.mObjfmast.EffectiveDate != null) // 07/Oct/2020 SCR No.145813
          {
            this.mObjfmast.EffectiveDate = new Date(this.mObjfmast.EffectiveDate);
          }
        }
        catch (e) {
          console.log(e);
        }
        //Begin Format date/time columns in customization grid field to remove "T"
        this.appUtility.bmlfTransformCustomFieldsGet(res.FMVarCost);
        this.appUtility.bmlfTransformCustomFieldsGet(res.FMItem);
        this.appUtility.bmlfTransformCustomFieldsGet(res.FMByProduct);
        //end Format date/time columns in customization grid field

        this.marrLineGridData = res.FMVarCost;
        this.marrFMItem = res.FMItem;
        this.marrLineItemGrid = res.FMItem;
        this.marrLineGridByProduct = res.FMByProduct;
        this.marrFMItemUOM = res.FMItemUOM
        this.marrBYProductUOM = res.BYProductUOM;
        this.marrClassification = res.ClassificationDetails;
        this.mArrAnalysisData = res.CSCostAnalysis;

        if (this.marrLineGridByProduct.length > 0) {
          this.marrLineGridByProduct.forEach((element) => {
            if (element.hasOwnProperty('uomUnit')) { this.marrUOM = element.uomUnit; }
            if (!element.hasOwnProperty('oldUnit')) { element.oldUnit = element.DisplayUOM; }
            else {
              //this.marrUOM = this.marrBYProductUOM.filter(x => x.Itemkey == element.ItemKey)
              //element.uomUnit = this.marrUOM;
              setTimeout(() => {
                this.marrUOM = this.marrBYProductUOM.filter(x => x.Itemkey == element.ItemKey)
                element.uomUnit = this.marrUOM;
              });
            }
          });
        }
        if (this.marrLineItemGrid.length > 0) {
          this.marrLineItemGrid.forEach((element) => {
            if (element.LineType == "1") {
              if (!element.hasOwnProperty('uomUnit')) {
                if (this.marrFMItemUOM.length > 0) {
                  //element.uomUnit = this.marrFMItemUOM.filter(x => x.Itemkey == element.LineKey);
                  // 21/jan/2021 scr no.149691
                  setTimeout(() => {
                    element.uomUnit = this.marrFMItemUOM.filter(x => x.Itemkey == element.LineKey);
                  });
                }
              }
              if (!element.hasOwnProperty('oldUnit')) {
                element.oldUnit = element.DisplayUOM;
              }
            }
          });
        }
        this.bmlfCheckEsignApplicableOnForm();// For check E-signature after save (Bug 177413)
        console.log(this.marrLineItemGrid);
        this.BMLFCalcItemSummary();
        this.BMLFCalcByProductSummary();

        this.bmlfgetDescription();

        let DBName = JSON.parse(localStorage.getItem('usersession')).CmpDbName;
        let pobjReqData = {
          ScreenName: 'ProductCostAnalysisComponent',
          LookUpKey: 'UserID',
          Filter1: DBName
        }
        this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
          if (!this.isEmpty(this.mObjfmast.recowner)) {
            let User_Desc = res.LookupData.filter(x => x.User_ID == this.mObjfmast.recowner);
            if (User_Desc.length > 0) {
              this.mObjfmast.user_desc = User_Desc[0].User_Description;
            }
          }
        });

        if (this.msCopy == ACTION.YES) {
          this.mbPKisDisable = false;
          this.msCopy = this.mObjfmast.FormulaID = '';
          setTimeout(function () { document.getElementById('txtFormulaID').focus(); }, 10);
          this.mObjfmast.FormulaID = '';
          //this.mbIsChange =true; // 29/Oct/2020 SCR No.146619
        }
        if (this.msCopy != ACTION.YES) {
          await this.bmlfswitchTab(this.mstabType);
        }

        // 22/Sep/2020 for scr no.144972
        if (this.msCurrentViewMode == "W" || this.msCurrentViewMode == "V") {
          let reqData: any = {};
          reqData = this.bmlfRequestJson('VIEWMODE');
          reqData.HeaderData[0]['ChangeViewMode'] = this.msCurrentViewMode;
          reqData.HeaderData[0]['CurrentViewMode'] = 'Q';
          let pSrequestObj: any = { JSON: JSON.stringify(reqData) };
          this.formulaEntryService.BMLFChangeViewMode(pSrequestObj).subscribe(res => {
            if (res == null) { return false; }
            else {
              if (this.isEmpty(res.Response[0].ErrorCode)) {
                this.bmlfFormatResponse(res, 'VIEWMODE');
              }
            }
          });
        }
      }
      else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
        return;
      }
    })
  }



