  // on Search335
  bmlfonSearch() {
    this.msAction = '';
    this.mbIsSearch = true;
    let reqData: any = {
      ScreenName: LookupKey.ScreenName,
      LookUpKey: 'ProductCostAnalysisList',
      Filter1: JSON.parse(localStorage.usersession).UserId
    };
    this.mslookupKeyData.searchValue = '';
    this.lookupService.getLookup(reqData).subscribe(res => {
      this.mbisLoading = false;
      if (res == null) {
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {

        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msbmeDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msbmeDateFormat.toUpperCase());
              }
            }
          })
        })

        res.LookupData.map(ele => {
          delete ele.data;
        });

        this.mArrLookupGridData = res.LookupData;
        this.mslookupKeyData.gridData = this.mArrLookupGridData;
        this.mslookupKeyData.field = LookupKey.ScreenName;
        this.mbisLookupModal = true;
      }
    }, err => {
      console.log(err);
    })
    this.mbisLoading = false;
  }

