        LineItemByProduct.CUSTOM3 = false;
        LineItemByProduct.CUSTOM4 = false;
        LineItemByProduct.CUSTOM5 = false;
        LineItemByProduct.CUSTOM6 = false;
        LineItemByProduct.CUSTOM7 = false;
        LineItemByProduct.CUSTOM8 = false;
        LineItemByProduct.CUSTOM9 = false;
        LineItemByProduct.CUSTOM10 = false;
        marrGridByProdtmp.push(LineItemByProduct);
