  //#region Save Button to Insert New Record  or Update Existing Record
  bmlfonSaveChanges(): any {
    ;
    this.mbIsCostMethodChange = false;
    if (this.bmlfisValidData() == 'YES') {
      if (this.mbIsChange || this.FieldExtender.mbChange || this.customTab1Ctrl.mbChange || this.customTab2Ctrl.mbChange || this.customTab3Ctrl.mbChange || this.customTab4Ctrl.mbChange || this.customTab5Ctrl.mbChange || this.customTab6Ctrl.mbChange || this.customTab7Ctrl.mbChange) {
        let psViewMode = 'By Quantity';
        if (this.msCurrentViewMode == "W") {
          psViewMode = 'By Weight %';
          // 30/Oct/2020 SCR No.146635
          // let psSumLQtyLine = this.marrLineItemGrid.filter(item => item.LineType === "1").reduce((sum, current) => sum + current.QtyInStockUOM, 0);
          // if(Number(psSumLQtyLine)>Number(100)){
          //   this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.SUMPERCENT'));
          //   return;
          // }
        } else if (this.msCurrentViewMode == "V") {
          psViewMode = 'By Volume %';
        }

        // End 18/jul/2020
        if (this.mbIsEsignature) {
          this.bmlfopenESignatureModal();
          return false;
        }
        // End 18/jul/2020
        this.BMLFNewCalcItemSummary();

        let reqData: any = {};
        reqData = this.bmlfRequestJson('SAVE');
        if (reqData.FMMast.length > 0) {//SCR 167662 Avanish 24Nov22
          reqData.FMMast[0]['ViewMode'] = psViewMode;
        }
        try       // 21/jan/21 scr no.149665
        {
          this.mObjfmast.EffectiveDate = moment(new Date(this.mObjfmast.EffectiveDate)).format('DD/MMM/YYYY');
        }
        catch (e) {

        }
        let pSrequestObj: any = { JSON: JSON.stringify(reqData) };
        if (!this.mbPKisDisable) {
          this.productCostAnalysisService.BMGFInsertCostAnalysis(pSrequestObj).pipe(tap(() =>
            this.mbIsLoading = true), delay(1000)).subscribe(res => {
              this.mbIsLoading = false;
              if (res == null) { return false; }
              else {
                if (res.Response[0].ErrorCode == "")   //21/Sep/2020
                {
                  if (res.Response[0].ResponseText.toUpperCase().includes('SUCCESS')) {
                    ////;
                    this.mbPKisDisable = true;
                    this.mbIsFMRevisionSetup;
                    this.mbIsChange = false;
                    this.customTab1Ctrl.mbChange = false;
                    this.customTab2Ctrl.mbChange = false;
                    this.customTab3Ctrl.mbChange = false;
                    this.customTab4Ctrl.mbChange = false;
                    this.customTab5Ctrl.mbChange = false;
                    this.customTab6Ctrl.mbChange = false;
                    this.customTab7Ctrl.mbChange = false;
                    this.msFormulaID = res.FMMast[0].FormulaID;
                    this.msRevisionNumber = res.FMMast[0].RevisionNumber;
                    this.bmlfSaveExtenderData(this.mObjfmast.FormulaID);
                    if (!this.isEmpty(this.msAction)) {
                      this.bmlfOthrAction(this.msAction);
                      this.msAction = '';
                    } else {
                      this.bmlfGetCostAnalysisDetailByID();
                    }

                    this.toastMessageService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.MSG.SAVE'));
                  } else if (!this.isEmpty(res.Response[0].ErrorCode)) {
                    this.toastMessageService.notifyError(res.Response[0].ResponseText);
                  }
                }
                else {
                  this.toastMessageService.notifyError(res.Response[0].ResponseText);
                }
              }
            });
        } else {
          this.mbIsLoading = true;
          this.productCostAnalysisService.BMGFUpdateCostAnalysis(pSrequestObj).subscribe(res => {
            this.mbIsLoading = false;
            if (res == null) { return false; }
            if (res.Response[0].ErrorCode == "")   //21/Sep/2020
            {
              this.mbIsChange = false;
              this.customTab1Ctrl.mbChange = false;
              this.customTab2Ctrl.mbChange = false;
              this.customTab3Ctrl.mbChange = false;
              this.customTab4Ctrl.mbChange = false;
              this.customTab5Ctrl.mbChange = false;
              this.customTab6Ctrl.mbChange = false;
              this.customTab7Ctrl.mbChange = false;
              this.toastMessageService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.MSG.SAVE'));
              this.bmlfSaveExtenderData(this.mObjfmast.FormulaID);
              if (!this.isEmpty(this.msAction)) {
                this.bmlfOthrAction(this.msAction);
                this.msAction = ''
              }
              else {
                if (res.FMMast.length > 0) {
                  this.msFormulaID = res.FMMast[0].FormulaID
                  this.msRevisionNumber = res.FMMast[0].RevisionNumber
                }
                this.bmlfGetCostAnalysisDetailByID();
              }
            }
            else {
              this.toastMessageService.notifyError(res.Response[0].ResponseText);
            }
          });
        }
        //this.bmlfswitchTab(this.mstabType);
      }
    }


  }
