  bmlfSortGrid() {
    this.marrLineGridData.sort((a, b) => {
      if (a.Maxwt > b.Maxwt) { return 1; } else if (a.Maxwt < b.Maxwt) { return -1; }
      else { return 0; }
    });
  }

  // Add Line in Variable Cost Grid
  bmlfAddVariableCost() {
    if (!this.isEmpty(this.mObjfmast.FormulaID)) {
      this.mObjfmvarCst = new FMVarCost();
      this.mObjfmvarCst.FormulaID = this.mObjfmast.FormulaID;
      this.mObjfmvarCst.isNewRecord = 'YES';
      if (this.marrLineGridData.length == 0) {
        this.mObjfmvarCst.Maxwt = 500;
      }
      if (this.marrLineGridData.length > 0) {
        this.mObjfmvarCst.Maxwt = Number(this.marrLineGridData[this.marrLineGridData.length - 1]["Maxwt"]) + 1;
      }

      this.marrLineGridData.push(this.mObjfmvarCst);
      if (this.marrLineGridData.length > 1) {
        let index = this.marrLineGridData.length - 2;
        if (!this.isEmpty(this.marrLineGridData[index]['LaborCostID']) || !this.isEmpty(this.marrLineGridData[index]['OverHeadID'])) {
          let eleID = 'txtMaxwt' + (this.marrLineGridData.length - 1).toString();
          setTimeout(() => { document.getElementById(eleID).focus(); }, 100);

        } else {
          this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('COST.ADDLINE_VALID'));
          this.marrLineGridData.splice(index, 1);
          this.mObjfmvarCst.Maxwt = Number(this.marrLineGridData[this.marrLineGridData.length - 1]["Maxwt"]) - 1;
          return false;
        }

      }
    } else {
      this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.FORMULAKEY'));
      return false;
    }
  }

  bmlfonOriginalAmtOnChangesFn(psIndex: any) {
    if (!this.isEmpty(this.marrLineGridData[psIndex]["Maxwt"])) {
      let psNewindex = psIndex - 1;
      if (!this.isEmpty(this.marrLineGridData[psIndex]["Maxwt"]) <= !this.isEmpty(this.marrLineGridData[psNewindex]["Maxwt"])) {
        this.marrLineGridData[psIndex]["Maxwt"] = this.marrLineGridData[psNewindex]["Maxwt"] + 1;
      }
    } else {
      this.marrLineGridData[psIndex]["Maxwt"] = this.marrLineGridData[psIndex - 1]["Maxwt"] + 1;
    }
  }

