  async bmlfCalExtCost(psObjKeyName: any = null, Psindex: any, event = null) {
    ////;
    //this.bmlfRevisionSetup(this.mstabType, psObjKeyName);
    if (this.mstabType == TAB_TYPE.LINEITEM) {
      this.msPreviousVal = event;
      this.marrLineItemGrid[Psindex]["ExtCost"] = (event * this.marrLineItemGrid[Psindex]["CostPerUnit"]);
      //this.mObjfmtotal.fmtotalRMCost = this.marrLineItemGrid.filter(x => x.LineType == "1").reduce(function (tot, arr) { return tot + arr.ExtCost; }, 0);
      //this.marrLineItemGrid[Psindex].PercentCost = ((this.marrLineItemGrid[Psindex].ExtCost) / this.mObjfmtotal.fmtotalRMCost) * 100;
      this.marrUOM = this.marrLineItemGrid[Psindex]['uomUnit'];
      await this.bmlfgetUOMByLineKey();

    } if (this.mstabType == TAB_TYPE.BYPRODUCT) {
      ////;
      this.msPreviousVal = event;
      this.marrLineGridByProduct[Psindex]["ExtCost"] = (event * this.marrLineGridByProduct[Psindex]["CostPerUnit"]);
      //this.mObjfmtotal.fmtotalRMCost = this.marrLineGridByProduct.reduce(function (tot, arr) { return tot + arr.ExtCost; }, 0);
      this.marrLineGridByProduct[Psindex]["PercentCost"] = (((event * this.marrLineGridByProduct[Psindex]["CostPerUnit"])) % (this.marrLineGridByProduct[Psindex]["CostPerUnit"])) * 100;
      this.marrUOM = this.marrLineGridByProduct[Psindex]['uomUnit'];
      await this.bmlfgetUOMByLineKey();
    }
  }


  bmlfCalExtCostDel(Psindex: any) {
    if (!this.isEmpty(this.marrLineItemGrid[Psindex]["QtyInDisplayUOM"])) {
      this.marrLineItemGrid[Psindex]["ExtCost"] = (this.marrLineItemGrid[Psindex]["QtyInDisplayUOM"] * this.marrLineItemGrid[Psindex]["CostPerUnit"]);
      this.marrLineItemGrid[Psindex]["PercentCost"] = (((this.marrLineItemGrid[Psindex]["QtyInDisplayUOM"] * this.marrLineItemGrid[Psindex]["CostPerUnit"])) % (this.marrLineItemGrid[Psindex]["CostPerUnit"])) * 100;

      let ExtCost = this.marrLineItemGrid.reduce(function (tot, arr) { return tot + arr.ExtCost; }, 0);
      //this.mObjfmtotal.fmtotalRMCost = ExtCost;
      //this.mObjfmtotal.fmtotalWeight = this.marrLineItemGrid
      // .reduce(function (tot, arr) { return tot + arr.QtyInDisplayUOM; }, 0);

      let len = this.marrLineItemGrid.length - 1;
      while (len >= 0) {
        let val = this.marrLineItemGrid[len]["ExtCost"];
        this.marrLineItemGrid[len]["PercentCost"] = ((val * 100 / ExtCost));
        len--
      }
    }
  }

