  bmlfItemLineValidation(): boolean {
    ////;
    let len = this.marrLineItemGrid.length - 1;
    let psisValid: boolean = true;
    while (len >= 0) {
      if (this.isEmpty(this.marrLineItemGrid[len]["LineKey"]) && this.marrLineItemGrid[len]["LineType"] == 1) {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.ITEM_KEY'));
        psisValid = false;
        break;
      } if (this.isEmpty(this.marrLineItemGrid[len]["LineKey"]) && this.marrLineItemGrid[len]["LineType"] == 2) {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.LABOUR_KEY'));
        psisValid = false;
        break;
      } if (this.isEmpty(this.marrLineItemGrid[len]["LineKey"]) && this.marrLineItemGrid[len]["LineType"] == 3) {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.BOILERPLATE'));
        psisValid = false;
        break;
      }
      //    if (this.isEmpty(this.marrLineItemGrid[len]["LocationKey"]) &&  this.marrLineItemGrid[len]["LineType"]!=4) {
      //     this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.LOCATION_KEY'));
      //    psisValid = false;
      //    break;
      //  }
      len--
    }
    return psisValid;
  }

  bmlfByProductValidation(): boolean {
    if (this.marrLineGridByProduct.length > 0) {
      for (let index = 0; index <= this.marrLineGridByProduct.length - 1; index++) {
        let j = index + 1;
        if (this.isEmpty(this.marrLineGridByProduct[index].ItemKey)) {
          this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.ITEM_KEY') + ' at RowNo ' + j);
          return false;
        }
        else if (this.isEmpty(this.marrLineGridByProduct[index].LocationKey)) {
          this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.LOCKEY') + ' at RowNo ' + j);
          return false;
        }
      }
      return true;
    }
    else {
      return true;
    }
  }

  bmlfonAddAttributes(id: any) {
    if (!document.getElementById(id).hasAttribute('disabled')) {
      document.getElementById(id).setAttribute('disabled', 'true');
    }
  }
  bmlfonRemoveAttributes(id: any) {
    if (document.getElementById(id).hasAttribute('disabled')) {
      document.getElementById(id).removeAttribute('disabled');
    }
  }

  bmlfonRemoveCSS(id: any) {
    if (document.getElementById(id).classList.contains('disabled')) {
      document.getElementById(id).classList.remove('disabled');
    }
  }
  bmlfonAddCSS(id: any) {
    if (!document.getElementById(id).classList.contains('disabled')) {
      document.getElementById(id).classList.add('disabled');
    }
  }


  //#region LINE ITEM TAB
  async bmlfEnableDisableGridCtl() {
    try {
      if (this.marrLineItemGrid.length > 0) {
        this.marrLineItemGrid.forEach((element, index) => {
          if (element.LineType == "1") {
            this.bmlfonRemoveAttributes('txtLineKey' + index);
            this.bmlfonRemoveCSS('cmdLineKeyLookup1' + index);
            this.bmlfonAddAttributes('txtareaLiteralText' + index);
            this.bmlfonRemoveAttributes('txtLocationKey' + index);
            this.bmlfonRemoveAttributes('txtQtyInDisplayUOM' + index);
            this.bmlfonRemoveAttributes('txtOverheadID' + index);
            this.bmlfonRemoveAttributes('cmdLocationKeyLookup' + index);
          } else if (element.LineType == "2") {
            this.bmlfonRemoveAttributes('txtLineKey' + index);
            this.bmlfonAddAttributes('txtareaLiteralText' + index);
            this.bmlfonAddAttributes('txtLocationKey' + index);
            this.bmlfonRemoveAttributes('txtQtyInDisplayUOM' + index);
            this.bmlfonRemoveAttributes('txtOverheadID' + index);
            this.bmlfonAddCSS('cmdLocationKeyLookup' + index);
            this.bmlfonAddAttributes('cmbDisplayUOM' + index);
          } else if (element.LineType == "3") {
            this.bmlfonRemoveAttributes('txtLineKey' + index);
            this.bmlfonRemoveCSS('cmdLineKeyLookup' + index);
            this.bmlfonAddAttributes('txtareaLiteralText' + index);
            this.bmlfonAddAttributes('txtLocationKey' + index);
            this.bmlfonAddAttributes('txtQtyInDisplayUOM' + index);
            this.bmlfonAddAttributes('txtOverheadID' + index);
            this.bmlfonAddCSS('cmdLocationKeyLookup' + index);
            this.bmlfonAddAttributes('cmbDisplayUOM' + index)
          } else if (element.LineType == "4") {
            this.bmlfonAddAttributes('txtLineKey' + index);
            this.bmlfonAddCSS('cmdLineKeyLookup' + index);
            this.bmlfonRemoveAttributes('txtareaLiteralText' + index);
            this.bmlfonAddAttributes('txtLocationKey' + index);
            this.bmlfonAddAttributes('txtQtyInDisplayUOM' + index);
            this.bmlfonAddAttributes('txtOverheadID' + index);
            this.bmlfonAddCSS('cmdLocationKeyLookup' + index);
            this.bmlfonAddAttributes('cmbDisplayUOM' + index);
          }
        });
        await this.bmlfgetUOMByLineKey();
      }
    }
    catch (e) {
      console.log('bmlfEnableDisableGridCtl : ' + e)
    }

  }

  //#endregion


