  bmlfonToggleBarFunc(psItemtxt: any, psItemid: any) {
    ;
    if (psItemtxt == HEADER.RESEQUENCE) {
      if (this.mstabType == TAB_TYPE.LINEITEM) {
        this.marrLineItemGrid.sort((a, b) => { if (a.SequenceNum > b.SequenceNum) { return 1; } else if (b.SequenceNum > a.SequenceNum) { return -1; } })
        this.bmlfEnableDisableGridCtl();
        this.marrLineItemGrid.forEach((element, index) => {
          element.SequenceNum = index + 1;
        });
        this.mbIsChange = true;
        //this.toastMessageService.notifySuccess(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.ITEMSORT'));
      } else {
        this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.LINETAB'));
      }
    } else if (psItemtxt == HEADER.SIZE) {
      if (this.mstabType == TAB_TYPE.LINEITEM || this.mstabType == TAB_TYPE.BYPRODUCT) {
        if (this.msCurrentViewMode != "Q") // 12/nov/2020 SCR No.147098
        {
          this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VIEWMODESIZE'));
          return;
        }
        this.bmlfloadSFormula();
      }
      else        // 25/jan/2021 scr no.149786
      {
        this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.LINEORBYPRDTAB'));
      }
    } else if (psItemtxt == HEADER.SORDER) {
      if (this.mbPKisDisable) {

        let pobjReqData = {
          ScreenName: 'ProductCostAnalysisComponent',
          LookUpKey: 'SODetails',
          Filter1: this.mObjfmast.FormulaID
        }
        this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
          ////;
          if (res.LookupData.length > 0) {
            this.marrSalesOrderGridData = res.LookupData
          }
        });
      } else {
        setTimeout(function () { document.getElementById('txtFormulaID').focus(); }, 10);
        this.mstabType = 'formula_master';
        this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.MSG.NODATA'));
      }

    } else if (psItemtxt == HEADER.AITEM) {
      this.modalService.open("AlternateItem");
    } else if (psItemtxt == HEADER.ABOMS) {
      this.bmlfgetAssociatedBOM();
    } else if (psItemtxt == HEADER.CREATEINTER) {
      if (!this.isEmpty(this.mObjfmast.FormulaID)) {

        this.bmlfCreateIntermediate();
      } else {
        setTimeout(function () { document.getElementById('txtFormulaID').focus(); }, 10);
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.FORMULAKEY'));
        return false;
      }
    } else if (psItemtxt == HEADER.SDS) {
      this.mbIsSdsBoilerpateTxt = true;
      //  this.modalService.open("SDSBoilerplateText");
    } else if (psItemtxt == HEADER.QUALITY) {
      if (this.isEmpty(this.mObjfmast.FormulaID)) {
        setTimeout(function () { document.getElementById('txtFormulaID').focus(); }, 10);
        this.mstabType = 'formula_master';
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.FORMULAKEY'));
        return;
      }
      if (this.mbPKisDisable == true) {
        let pobjReqData = {
          ScreenName: 'ProductCostAnalysisComponent',
          LookUpKey: 'QCTestData',
          Filter1: this.mObjfmast.FormulaID
        }
        this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
          if (res.LookupData.length > 0) {
            this.router.navigate(['/main/qualitycontrol/qcformulatests/manage'], { queryParams: { ITEMKEY: this.mObjfmast.FormulaID, description: this.mObjfmast.Description } });
          }
          else {
            this.router.navigate(['/main/qualitycontrol/qcformulatests/manage'], { queryParams: { ITEMKEY: this.mObjfmast.FormulaID, description: this.mObjfmast.Description, AddRecord: "Y" } });
          }
        });
      }
      else {
        this.toastMessageService.notifyError('Record not saved,cannot open Quality Control.');
      }

    } else if (psItemtxt == HEADER.CREATE) {
      this.bmlfCreateFG();
      // this.modalService.open("CreateFinishedGood");
    }
    else if (psItemtxt == 'Send for Approval') {
      if (this.isEmpty(this.mObjfmast.FormulaID)) {
        setTimeout(function () { document.getElementById('txtFormulaID').focus(); }, 10);
        this.mstabType = 'formula_master';
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.FORMULAKEY'));
        return;
      }
      if (this.mObjfmast.FMstate != 'ACTIVE') {
        this.bmlfSendforApproval().then(() => {
          this.bmlfGetCostAnalysisDetailByID();
        });
      }
      else {
        this.toastMessageService.notifyError('Formula is already Active.');
        return;
      }



    }
    else if (psItemtxt == 'Transfer To Quotation') {

      console.log(this.mArrParaMeters);
      if (this.mstabType == TAB_TYPE.ANALYSIS) {
        let noItems = true;
        if (this.mArrParaMeters[0]['ItemKey'].trim() != '' || this.mArrParaMeters[1]['ItemKey'].trim() != '' || this.mArrParaMeters[2]['ItemKey'].trim() != '') {
          noItems = false;
        }
        if (noItems == false) {
          //SellingPrice
          this.bmlfGetCSQuotationData();
        }
        else {
          this.toastMessageService.notifyError('Select Item(s) for Quotation.');
          return;
        }

      }
      else {
        this.toastMessageService.notifyError('Please select Analysis Tab.');
        return;
      }

    }
    else if (psItemtxt == 'Print Analysis') {

      if (this.mstabType == 'Analysis') {
        this.printDiv('printableArea')
      }

    }
    else if (psItemtxt == 'Get Default Values') {

      this.bmlfGetCostAnalysisDetailByID();

      this.mArrParaMeters[0]['FillLevel'] = this.mArrParaMeters[0]['OrgFillLevel'];
      this.mArrParaMeters[1]['FillLevel'] = this.mArrParaMeters[1]['OrgFillLevel'];
      this.mArrParaMeters[2]['FillLevel'] = this.mArrParaMeters[2]['OrgFillLevel'];

      this.bmlfPerformCostAnalysis();
    }
  }



