  dialogalterAction(psAction: any) {
    ////;
    this.mbalternateDialogView = false;
    if (this.msDelType == ACTION_LINE.COPY) {
      if (psAction == ACTION.YES && this.bmlfItemLineValidation()) {
        this.mObjfmItem = new FMItem();
        this.marrLineItemGrid.sort((a, b) => { if (a.SequenceNum > b.SequenceNum) { return 1; } else if (a.SequenceNum > b.SequenceNum) { return -1; } });
        this.mObjfmItem = this.marrLineItemGrid[this.mslineItemRowIndex]
        this.mObjfmItem.SequenceNum = this.marrLineItemGrid[this.marrLineItemGrid.length - 1]["SequenceNum"] + 1;
        this.marrLineItemGrid.push(this.mObjfmItem);
        this.bmlfCalExtCost(null, null, this.mslineItemRowIndex);
        this.bmlfnGgLineStatus(this.mslineItemRowIndex, this.mObjfmItem.LineType, null);
      }
    } else if (this.msDelType == ACTION_LINE.CREATE) {
      if (psAction == ACTION.YES && this.bmlfItemLineValidation()) {
        this.mObjfmItem = new FMItem();
        //this.marrLineItemGrid.sort((a, b) => { if (a.SequenceNum > b.SequenceNum) { return 1; } else if (a.SequenceNum > b.SequenceNum) { return -1; } });

        this.mObjfmItem = Object.assign({}, this.marrLineItemGrid[this.mslineItemRowIndex]);
        //this.mObjfmItem = this.marrLineItemGrid[this.mslineItemRowIndex]
        this.mObjfmItem.SequenceNum = this.marrLineItemGrid[this.marrLineItemGrid.length - 1]["SequenceNum"] + 1;
        this.marrLineItemGrid.push(this.mObjfmItem);
        this.BMLFCalcItemSummary();
        // this.bmlfCalExtCost(this.mslineItemRowIndex,this.mObjfmItem);
        // 19/jan/21 scr no.149671
        if (this.mArrParaMeters[0]['ItemKey'].trim() != '' || this.mArrParaMeters[1]['ItemKey'].trim() != '' || this.mArrParaMeters[2]['ItemKey'].trim() != '') {
          this.mbcalPropanalysis = true;
        }
        this.bmlfnGgLineStatus(this.marrLineItemGrid.length - 1, this.mObjfmItem.LineType, this.msDelType);

      }
      //  else{
      //   this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.LINE_ITEM_VALID'));
      //   return false;
      // }
    }
  }
