  bmlfloadSFormula() {
    //    console.log(this.marrLineItemGrid);
    let psRowWeight = this.marrLineItemGrid.filter(x => x.LineType == "1").reduce(function (tot, arr) { return tot + Number(parseFloat(arr.QtyInDisplayUOM).toFixed(3)) }, 0);
    if (psRowWeight > 0) {
      this.bmlfSpecfuncDensityCal('');
      this.modalService.open("sizeformula");
      this.mbWtSizeFormula = true;
      this.mbVoluSizeFormula = false;
      this.mObjSizeFormula.Sizeby = 'Weight';
      this.mObjSizeFormula.TotalSystemWt = this.mObjFormulaLineTotal.fmtotalWeight;
      this.mObjSizeFormula.TotalSystemVol = this.mObjFormulaLineTotal.fmtotalVolume;
      this.mObjSizeFormula.NewSystemWt = this.mObjFormulaLineTotal.fmtotalWeight;
      this.mObjSizeFormula.NewSystemVol = this.mObjFormulaLineTotal.fmtotalVolume;
    }
    // else {
    //   this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.SIZE'));
    // }
  }
  bmlfChangeFBySize(event, ConversionType: any) {
    ////;
    if (ConversionType == 'Weight' && event.target.checked) {
      this.mbWtSizeFormula = true;
      this.mbVoluSizeFormula = false;
    } else if (ConversionType == 'Volume' && event.target.checked) {
      this.mbWtSizeFormula = false;
      this.mbVoluSizeFormula = true;
    }
  }

  bmlfonChangeSize(value, ConversionType: any) {
    if (!this.isEmpty(value) && ConversionType == 'Weight') {
      this.mObjSizeFormula.Sizeby = ConversionType;
      this.mObjSizeFormula.NewSystemWt = value;
    } else if (!this.isEmpty(value) && ConversionType == 'Volume') {
      this.mObjSizeFormula.Sizeby = ConversionType;
      this.mObjSizeFormula.NewSystemVol = value;
    }
  }

