  //function to calculate size formula
  async bmlCalsizeformula() {

    let NewSystemWt = this.mObjSizeFormula.NewSystemWt.toFixed(this.miQtyPrecision);
    let NewSystemVol = this.mObjSizeFormula.NewSystemVol.toFixed(this.miQtyPrecision);
    if (Number(NewSystemWt) > 0 && this.mbWtSizeFormula == true) // 15/Oct/2020 SCR No.144019
    {
      let reqData: any = this.bmlfRequestJson('SIZE');
      let pSrequestObj: any = { JSON: JSON.stringify(reqData) };
      this.productCostAnalysisService.BMLFSizeFormula(pSrequestObj).pipe(
        tap(() => this.mbIsLoading = true), delay(0)).subscribe(async res => {
          this.mbIsLoading = false;
          this.modalService.close("sizeformula");
          this.mbIsChange = true; //08/feb/2021 SCR No.150164
          //console.log(res);
          if (res == null) { return false; }
          if (this.isEmpty(res.Response[0].ErrorCode)) {
            this.bmlfFormatResponse(res);
            this.bmlfgetUOMByLineKey();
            this.BMLFCalcItemSummary();
            this.mbIsCalculate = true;
            this.BMLFNewCalcItemSummary();
            this.BMLFCalcByProductSummary();
            if (this.mArrParaMeters[0]['ItemKey'].trim() != '' || this.mArrParaMeters[1]['ItemKey'].trim() != '' || this.mArrParaMeters[2]['ItemKey'].trim() != '') {
              //this.bmlfPerformCostAnalysis();
              this.mbcalPropanalysis = true;
              this.mbIsChange = true; //29/Sep/2020 SCR No.143665
            }
          }
        });
    }
    else if (Number(NewSystemVol) > 0 && this.mbVoluSizeFormula == true) // 15/Oct/2020 SCR No.144019
    {
      let reqData: any = this.bmlfRequestJson('SIZE');
      let pSrequestObj: any = { JSON: JSON.stringify(reqData) };
      this.productCostAnalysisService.BMLFSizeFormula(pSrequestObj).pipe(
        tap(() => this.mbIsLoading = true), delay(0)).subscribe(async res => {
          this.mbIsLoading = false;
          this.modalService.close("sizeformula");
          this.mbIsChange = true; //15/feb/2021 SCR No.150444 deepak porwal
          //console.log(res);
          if (res == null) { return false; }
          if (this.isEmpty(res.Response[0].ErrorCode)) {
            this.bmlfFormatResponse(res);
            this.bmlfgetUOMByLineKey();
            this.BMLFCalcItemSummary();
            this.mbIsCalculate = true;
            this.BMLFNewCalcItemSummary();
            this.BMLFCalcByProductSummary();
            if (this.mArrParaMeters[0]['ItemKey'].trim() != '' || this.mArrParaMeters[1]['ItemKey'].trim() != '' || this.mArrParaMeters[2]['ItemKey'].trim() != '') {
              //this.bmlfPerformCostAnalysis();
              this.mbcalPropanalysis = true;
              this.mbIsChange = true; //29/Sep/2020 SCR No.143665
            }
          }
        });
    }
    else {
      this.mbZeroSize = true;
    }

  }

  //#region Format the Response Data
  bmlfFormatResponse(resObj, RespnoseType: any = null) {
    if (resObj.FMItem.length > 0) {
      resObj.FMItem.forEach((element, index) => {
        this.marrLineItemGrid[index].QtyInDisplayUOM = this.appUtility.bmgfToFixNumConversion(element.QtyInDisplayUOM, 7);
        this.marrLineItemGrid[index].QtyInStockUOM = this.appUtility.bmgfToFixNumConversion(element.QtyInStockUOM, 7);
        this.marrLineItemGrid[index].CostPerUnit = this.appUtility.bmgfToFixNumConversion(element.CostPerUnit, 6);
        this.marrLineItemGrid[index].ExtCost = this.appUtility.bmgfToFixNumConversion(element.ExtCost, 6);
        this.marrLineItemGrid[index].PercentCost = this.appUtility.bmgfToFixNumConversion(element.PercentCost, 6);
        this.marrLineItemGrid[index].LossPcnt = this.appUtility.bmgfToFixNumConversion(element.LossPcnt, 6);
        this.marrLineItemGrid[index].SysWt = this.appUtility.bmgfToFixNumConversion(element.SysWt, 7);
        this.marrLineItemGrid[index].SysVol = this.appUtility.bmgfToFixNumConversion(element.SysVol, 7);
      });
    }
    if (this.isEmpty(RespnoseType)) {
      if (resObj.FMByProduct.length > 0) {
        resObj.FMByProduct.forEach((element, index) => {
          this.marrLineGridByProduct[index].QtyInDisPlayUOM = this.appUtility.bmgfToFixNumConversion(element.QtyInDisPlayUOM, 7);
          this.marrLineGridByProduct[index].QtyInStockUOM = this.appUtility.bmgfToFixNumConversion(element.QtyInStockUOM, 7);
          this.marrLineGridByProduct[index].CostPerUnit = this.appUtility.bmgfToFixNumConversion(element.CostPerUnit, 6);
          this.marrLineGridByProduct[index].ExtCost = this.appUtility.bmgfToFixNumConversion(element.ExtCost, 6);
          this.marrLineGridByProduct[index].SysWt = this.appUtility.bmgfToFixNumConversion(element.SysWt, 7);
          this.marrLineGridByProduct[index].SysVol = this.appUtility.bmgfToFixNumConversion(element.SysVol, 7);
        });
      }
    }
    this.bmlfCalculateTotalPercentage(); //Avanish 18Jul23  (2290-1160)
    // this.bmlfswitchTab(this.mstabType);
  }
