  // #region CreateIntermediate
  bmlfCreateIntermediate() {
    let psRowWeight = this.marrLineItemGrid.filter(x => x.LineType == "1").reduce(function (tot, arr) { return tot + arr.QtyInDisplayUOM; }, 0);
    if (psRowWeight > 0) {
      this.modalService.open("CreateIntermediate");
      this.bmlfSpecfuncDensityCal('');
      this.mObjCItermediate.FormulaKey = this.mObjfmast.FormulaID;
      this.mObjCItermediate.FormulaDesc = this.mObjfmast.Description;
      this.mObjCItermediate.ItemKey = this.mObjfmast.FormulaID;
      this.mObjCItermediate.ItemDesc = this.mObjfmast.Description;
      this.mObjCItermediate.DensityOverride = this.mObjFormulaLineTotal.fmsysWeight;
      this.mObjCItermediate.ConvFctr = this.mObjFormulaLineTotal.fmsysWeight;
      this.mObjCItermediate.FormulaClass = this.marrCIDefaultDetails[0].FrmlCls;
    } else {
      this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.CI'));
    }

  }


  bmlfCInterValidate(): boolean {
    if (this.isEmpty(this.mObjCItermediate.ItemKey)) {
      this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('TOOLBAR.FGOODS.ITEMKEY'));
      return false;
    } else if (this.isEmpty(this.mObjCItermediate.ItemLocation)) {
      this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('TOOLBAR.FGOODS.ITEMLOC'));
      return false;
    } else if (this.isEmpty(this.mObjCItermediate.UOMKey)) {
      this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('TOOLBAR.FGOODS.UOMKEY'));
      return false;
    } else { return true; }
  }

  bmlfInterSubmit() {
    if (this.bmlfCInterValidate()) {
      let reqData: any = { HeaderData: [], Response: [{ ResponseText: "", ErrorCode: "" }] }
      reqData.HeaderData.push(this.mObjCItermediate);
      let pSrequestObj: any = { JSON: JSON.stringify(reqData) };
      console.log(pSrequestObj);
      this.formulaEntryService.BMGFCreateIntremediate(pSrequestObj).subscribe(res => {
        if (res == null) { return false; }
        this.modalService.close("CreateIntermediate");
        this.mbIsLoading = false;
        this.msMsgData.Data = '';
        this.msMsgData.Data = res.Response[0].ErrorCode;
        this.mbBulkDeleteView = true;
      });
    }
  }

  bmlfcloseMsgModal(event) {
    this.mbBulkDeleteView = event.mbBulkDeleteView;
  }

