  bmlfGetPrecision() {
    try {
      let data = JSON.parse(localStorage.getItem('RoundingDefaults'));
      this.miQtyPrecision = data[0]["Default_Value"];
      this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision.toString();

      if (this.miQtyPrecision > Number(6)) {
        this.micurrencyPrecision = this.miQtyPrecision;
        this.micurrencyFormat = 'n' + this.miQtyPrecision.toString();
      }
      else {
        this.micurrencyPrecision = 6;
        this.micurrencyFormat = 'n6';
      }

      this.marrDefaultCost = JSON.parse(localStorage.getItem("CompanyDefaults"));
      let psCost: any;
      this.marrDefaultCost.forEach(element => {
        if (element.Default_Key == 'CURR_DECIMAL') {
          psCost = element.Default_Value;
        }
      });
      this.miCostPrecision = psCost;
      this.miCostFormat = 'n' + psCost.toString();
    }
    catch (e) {
      this.miQtyPrecision = 2;
      this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision.toString()
    }
  }

