  ValidateMaxWt(val: any, index) {
    if (this.marrLineGridData.length > 0) {
      if (this.marrLineGridData[index + 1] != undefined) {
        if (val > this.marrLineGridData[index + 1].Maxwt) {
          this.marrLineGridData[index].Maxwt = this.marrLineGridData[index].OrgMaxwt;
        }
        else {
          this.marrLineGridData[index].OrgMaxwt = this.marrLineGridData[index].Maxwt;
        }
      }
      this.mbIsChange = true;
    }
  }

  bmlfValidationonQty() {

    let psSumLQtyLine = this.marrLineItemGrid.filter(item => item.LineType === "1").reduce((sum, current) => sum + current.QtyInStockUOM, 0);
    let psSumByProduct = this.marrLineGridByProduct.reduce(function (tot, arr) { return tot + arr.QtyInStockUOM; }, 0);
    if (psSumLQtyLine > 0 && psSumByProduct > 0) {
      if (Number(psSumByProduct) >= Number(psSumLQtyLine)) {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.VALID.WGVALID'));
        return false;
      }
    }

    if (this.marrLineItemGrid.length > 0) {
      this.marrLineItemGrid.forEach((element, index) => {
        let j = index + 1;
        if (element.LineType == '1') {
          if (!this.isEmpty(element.LineKey) && element.hasOwnProperty('uomUnit')) {
            let psLtVol = element.uomUnit.filter(x => x.ToKey == this.msVolUOM)[0];
            let psKGVol = element.uomUnit.filter(x => x.ToKey == this.msWtUOM)[0];
            if (this.isEmpty(psLtVol)) {
              this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.CONVERSION') + ' ' + element.LineKey + ' at RowNo ' + j + ' in Line Items ');
              return;
            }
            if (this.isEmpty(psKGVol)) {
              this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.CONVERSION') + ' ' + element.LineKey + ' at RowNo ' + j + ' in Line Items ');
              return;
            }
          }
        }
        if (element.LineType == '2') {
          if (this.isEmpty(element.LineKey)) {
            this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.LABOUR_KEY') + ' at RowNo ' + j + '');


            return;
          }
        }
        if (element.LineType == '3') {
          if (this.isEmpty(element.LineKey)) {
            this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.BOILERPLATE') + ' at RowNo ' + j + ' Line Items');


            return;
          }
        }
      });
    }

  }

