  bmlfonCloseSizeRMCost(psId: any, callfor) {
    if (callfor == 'NoChange') {
      this.mObjFormulaLineTotal.fmtotalRMCost = this.mObjFormulaLineTotal.fmoldtotalRMCost;
    }
    this.modalService.close(psId);
  }

  bmlfChangeCostingType(event, ConversionType: any) {
    ////;
    if (ConversionType == 'Qty' && event.target.checked) {
      this.mbWtSizeCostByQty = true;
      this.mbWtSizeByCost = false;
    } else if (ConversionType == 'Cost' && event.target.checked) {
      this.mbWtSizeCostByQty = false;
      this.mbWtSizeByCost = true;
    }
  }

  bmlfSendforApproval() {
    return new Promise((resolve, reject) => {

      let pobjReqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            "FormulaID": this.mObjfmast.FormulaID,
            "Revisionnumber": this.mObjfmast.RevisionNumber,
            "EffectiveDate": this.mObjfmast.EffectiveDate,
            "RevisionDate": this.mObjfmast.RevisionDate,
            "RevisionTime": this.mObjfmast.RevisionTime,
            "RevisionStatus": this.mObjfmast.FMstate,
            "FormulaClass": this.mObjfmast.FormulaClassID
          }],
          Response: [{ ResponseText: '', ErrorCode: '' }]
        })
      };
      this.mbIsLoading = true;
      this.productCostAnalysisService.BMGFFormulaSendforApproval(pobjReqData).subscribe(res => {
        if (res == null) {
          this.mbIsLoading = false;
          reject();
          return false;
        }
        else {
          if (this.isEmpty(res.Response[0].ErrorCode)) {
            console.log('BMGFFormulaSendforApproval response');
            console.log(res);

            this.msFormulaID = res.HeaderData[0]['FormulaID'];
            this.msRevisionNumber = res.HeaderData[0]['Revisionnumber'];
            this.mbIsLoading = false;
            resolve('success');
          }
        }
      }), err => {
        console.log(err);
      };
    })
  }

  TransferQuotation() {

    if (this.msCustomer.trim() == '') {
      this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('PRODUCT_COST.ENTCUS'));
      return;
    }
    // 06/Oct/2020 SCR No.145763
    let selectedData = this.grdTranQuot.filter(ele => ele.Select);
    if (selectedData.length > 0) {
      let SelgrdTranQuot: any = [];
      var i;
      for (i = 0; i < this.grdTranQuot.length; i++) {
        if (this.grdTranQuot[i]['Select'] == true)
          // SelgrdTranQuot.push({
          //   ItemKey: this.grdTranQuot[i]['ItemKey'],
          //   Location: this.grdTranQuot[i]['Location'],
          //   PriceInDisp: this.grdTranQuot[i]['PriceInDisp'],
          //   PriceInSalesUOM: this.grdTranQuot[i]['PriceInSalesUOM'],
          //   QtyinSalesUOM: this.grdTranQuot[i]['QtyinSalesUOM'],
          //   SalesUOM: this.grdTranQuot[i]['SalesUOM'],
          // })
          // 06/Oct/2020 SCR No.145760
          SelgrdTranQuot.push({
            ItemKey: this.grdTranQuot[i]['ItemKey'],
            Location: this.grdTranQuot[i]['Location'],
            PriceInDisp: Number(this.grdTranQuot[i]['PriceInDisp']).toFixed(6),
            PriceInSalesUOM: Number(this.grdTranQuot[i]['PriceInSalesUOM']).toFixed(6),
            QtyinSalesUOM: Number(this.grdTranQuot[i]['QtyinSalesUOM']).toFixed(this.miQtyPrecision),
            SalesUOM: this.grdTranQuot[i]['SalesUOM'],
          })
      }

      let pobjReqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            "CustomerKey": this.msCustomer,
            "QuotationNo": this.msQuotNo
          }],
          Quotation: SelgrdTranQuot,
          Response: [{ ResponseText: '', ErrorCode: '' }]
        })
      };
      this.mbIsLoading = true;
      this.productCostAnalysisService.BMGFCostAnalysisTranToQuot(pobjReqData).subscribe(res => {
        if (res == null) {
          this.mbIsLoading = false;
          return false;
        }
        else {
          if (res.Response[0].ErrorCode == '') {

            this.msCustomer = '';
            this.msQuotNo = '';
            this.bmlfonCloseToolBar('TransToQuot');
            this.mbIsLoading = false;
            this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
          }
          else {
            this.mbIsLoading = false;
            if (res.Response[0].ResponseText == '') {
              this.toastMessageService.notifyError('Server error.');
              return;
            }
            else {
              this.toastMessageService.notifyError(res.Response[0].ResponseText);
              return;
            }
            this.msCustomer = '';
            this.msQuotNo = '';
            this.bmlfonCloseToolBar('TransToQuot');
            this.mbIsLoading = false;
          }
        }
      }), err => {
        console.log(err);
      };
    }
    else {
      this.toastMessageService.notifyError('Please select any LineItem to transfer.')
      return;
    }

  }

