  public mbResetDialog: boolean = false;
  public mbcalPropanalysis: boolean = false;

  BMGFPerformCostAnalysisWithoutValueReplacement(colid) {
    ;
    //begin
    let colData = [];
    if (colid == '0') {
      colData = this.mArrParaMeters[0];
    }
    else if (colid == '1') {
      colData = this.mArrParaMeters[1];
    }
    else if (colid == '2') {
      colData = this.mArrParaMeters[2];
    }
    if (this.mbIsMarkup == true) {
      colData['MarkupMargin'] = '1';
    }
    else {
      colData['MarkupMargin'] = '2';
    }

    colData['FillType'] = colData['FillMeasuredIn']; // SCR No.141165

    let reqData: any = {
      HeaderData: [colData],
      CSItemByProductSummary: [],
      CSCostAnalysis: [],
      Response: [{ ResponseText: "", ErrorCode: "" }]
    };

    // 01/Oct/2020 SCR No.144680
    //reqData.CSItemByProductSummary.push(this.msObjCSItemByProductSummary);
    reqData.CSItemByProductSummary.push({
      "ByProductCostPerSysVol": Number(this.msObjCSItemByProductSummary.ByProductCostPerSysVol).toFixed(this.miQtyPrecision),
      "ByProductCostPerSysWt": Number(this.msObjCSItemByProductSummary.ByProductCostPerSysWt).toFixed(this.miQtyPrecision),
      "ByProductSysWtPerSysVol": Number(this.msObjCSItemByProductSummary.ByProductSysWtPerSysVol).toFixed(this.miQtyPrecision),
      "ByProductTotalRMC": Number(this.msObjCSItemByProductSummary.ByProductTotalRMC).toFixed(6),
      "ByProductVolume": Number(this.msObjCSItemByProductSummary.ByProductVolume).toFixed(this.miQtyPrecision),
      "ByProductWeight": Number(this.msObjCSItemByProductSummary.ByProductWeight).toFixed(this.miQtyPrecision),
      "CostPerSysVol": Number(this.msObjCSItemByProductSummary.CostPerSysVol).toFixed(6),
      "CostPerSysWt": Number(this.msObjCSItemByProductSummary.CostPerSysWt).toFixed(6),
      "SysWtPerSysVol": Number(this.msObjCSItemByProductSummary.SysWtPerSysVol).toFixed(6),
      "TotalRMC": Number(this.msObjCSItemByProductSummary.TotalRMC).toFixed(6),
      "TotalVolume": this.msObjCSItemByProductSummary.TotalVolume,
      "TotalWeight": Number(this.msObjCSItemByProductSummary.TotalWeight).toFixed(this.miQtyPrecision)
    });
    let mArrAnalysisData = [];
    this.mArrAnalysisGridData.concat(this.mArrAnalysisGridFooterData).forEach((element) => {
      let LineItemdata: any = {};
      LineItemdata.FMMaterialDescription = element.FMMaterialDescription;
      let FMItem1 = element.FMItem1;
      LineItemdata.FMItem1 = this.isEmpty(FMItem1) ? 0.000000 : parseFloat(FMItem1).toFixed(6);
      let FMItem2 = element.FMItem2;
      LineItemdata.FMItem2 = this.isEmpty(FMItem2) ? 0.000000 : parseFloat(FMItem2).toFixed(6);
      let FMItem3 = element.FMItem3;
      LineItemdata.FMItem3 = this.isEmpty(FMItem3) ? 0.000000 : parseFloat(FMItem3).toFixed(6);
      mArrAnalysisData.push(LineItemdata)
    });
    reqData.CSCostAnalysis = mArrAnalysisData;
    //end

    let pSrequestObj: any = { JSON: JSON.stringify(reqData) };
    this.productCostAnalysisService.BMGFPerformCostAnalysisWithoutValueReplacement(pSrequestObj).pipe(
      tap(() => this.mbIsLoading = true), delay(3000)).subscribe(async res => {
        this.mbIsLoading = false;
        if (res == null) {
          this.mbResetDialog = false;
          return false;
        }
        else {
          this.bmlfFormatResData(res)
          this.mbResetDialog = false;
        }
      });
  }

