  ChangeMarkupMargin(evt, callfor) {

    if (callfor == 'Markup') {
      this.mbIsMarkup = true;
      this.mbIsMargin = false;
      this.mArrAnalysisGridFooterData[2]['FMMaterialDescription'] = 'Markup X 100 %';
      if (this.mArrParaMeters[0]['ItemKey'].trim() != '' || this.mArrParaMeters[1]['ItemKey'].trim() != '' || this.mArrParaMeters[2]['ItemKey'].trim() != '') {
        this.bmlfPerformCostAnalysis();
      }

    }
    else {
      this.mbIsMarkup = false;
      this.mbIsMargin = true;
      this.mArrAnalysisGridFooterData[2]['FMMaterialDescription'] = 'Margin Amount';
      if (this.mArrParaMeters[0]['ItemKey'].trim() != '' || this.mArrParaMeters[1]['ItemKey'].trim() != '' || this.mArrParaMeters[2]['ItemKey'].trim() != '') {
        let reqData: any = this.bmlfRequestJson('ANALYSIS2');
        let pSrequestObj: any = { JSON: JSON.stringify(reqData) };
        this.productCostAnalysisService.BMGFPerformCostAnalysis(pSrequestObj).pipe(
          tap(() => this.mbIsLoading = true), delay(3000)).subscribe(async res => {
            this.mbIsLoading = false;
            if (res == null) { return false; }
            else {
              this.bmlfFormatResData(res)
            }
          });
      }

    }

  }

