  //Begin 16/Sep/2020 For %Wt ,%Vol  on lineitem
  // Added an event in html file
  BMGFChangeQTYForWV(viewmode, oldValue, newValue, index) {
    return new Promise((resolve, reject) => {
      if (this.mstabType == TAB_TYPE.LINEITEM) {
        // 05/Oct/2020 scr no.145615
        // let psSumLQtyLine = this.marrLineItemGrid.filter(item => item.LineType === "1").reduce((sum, current) => sum + current.QtyInStockUOM, 0);
        // if(Number(psSumLQtyLine)>Number(100)){
        //   this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('LINEITEMS.VALID.SUMPERCENT'));
        //   return;
        // }
        let pobjReqData: any = {
          JSON: JSON.stringify({
            HeaderData: [
              {
                ViewMode: viewmode,
                OldValue: Number(oldValue).toFixed(this.miQtyPrecision),
                NewValue: Number(newValue).toFixed(this.miQtyPrecision),
                CurrentRowno: parseInt(index)
              }],
            FMItem: this.GetLineItemReqJSON(),
            LineSummary: [{
              "TotalWeight": this.mObjFormulaLineTotal.fmtotalWeight,
              "TotalVolume": this.mObjFormulaLineTotal.fmtotalVolume,
              "SysWtPerSysVol": this.mObjFormulaLineTotal.fmsysWeight,
              "CostPerSysWt": this.mObjFormulaLineTotal.fmcostWeight,
              "CostPerSysVol": this.mObjFormulaLineTotal.fmcostVolumne,
              "TotalRMC": this.mObjFormulaLineTotal.fmtotalRMCost
            }],
            Response: [{ ResponseText: '', ErrorCode: '' }]
          })
        };
        this.formulaEntryService.BMGFChangeQTYForWV(pobjReqData).subscribe(res => {

          if (res == null) {
            reject();
            return false;
          }
          else {
            if (this.isEmpty(res.Response[0].ErrorCode)) {
              if (res.FMItem.length > 0) {
                for (let index = 0; index < this.marrLineItemGrid.length; index++) {
                  this.marrLineItemGrid[index]['QtyInDisplayUOM'] = Number(res.FMItem[index]['QtyInDisplayUOM']);
                  this.marrLineItemGrid[index]['QtyInStockUOM'] = Number(res.FMItem[index]['QtyInStockUOM']);
                  this.marrLineItemGrid[index]['SysWt'] = Number(res.FMItem[index]['SysWt'].toString().replace(",", ""));
                  this.marrLineItemGrid[index]['SysVol'] = Number(res.FMItem[index]['SysVol'].toString().replace(",", ""));
                  this.marrLineItemGrid[index]['ExtCost'] = Number(res.FMItem[index]['ExtCost']);
                }
              }
              this.BMLFCalcItemSummary();
              resolve('success');
            }
            else {
              this.toastMessageService.notifyError((res.Response[0].ResponseText));
              reject();
            }
          }
        }), err => {
          console.log(err);
        };

      }

    })

  }

