  ngOnInit() {
    this.msScreenname = this.objUtils.bmgfonTranslate("UPDATEFINISHEDGOODCOSTS.STITLE");
    this.objUtils.getBMEScreePermission(this.msScreenname); // Fetch Screen permission
    this.bmlfGetRoundingDefaults();
    this.bmlfsetformLoadModel();
  }

  bmlfGetRoundingDefaults() {
    
    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    parrRoundingDefaults.forEach((ele) => {
      if (ele.Default_Key == 'Production') {
        this.miQtyPrecision = ele.Default_Value;
        this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;
      }
    });
  }

  bmlfsetformLoadModel() {

    let parrSession: any = [];
    parrSession = JSON.parse(localStorage.getItem('SCMPHData'));
    if (parrSession[0].FININTERFACE == true) {
      this.mbFinInterface = true;
    }

    this.msCustomDateFormat = this.dateFormatService.getDateFormat();

    this.mObjModel.UpdateAveCost = false;
    this.mObjModel.UpdateStdCost = false;
    this.mObjModel.UpdateLstCost = false;
    this.mObjModel.UpdateBasePrice = false;
    this.mObjModel.UpdateMarketCost = false;
    this.mObjModel.IncludeLaborAndOverhead = false;
    this.mObjModel.FMWeight = 0;
    this.mObjModel.UpdateMode = "TRIAL";

  }

  bmlfOpenErrorBlock(psMsg): void {
    this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate(psMsg));
  }

  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mObjModel[psField] = "";

      return false
    }

    let psReqData: any
    if (psLookUpKey == "Finished Good Key From") {
      psReqData = { "ScreenName": "Update Finished Good Costs", "LookUpKey": "Finished Good Key From", "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    } else if (psLookUpKey == "Finished Good Key To") {
      if (psEvent == 'click') { this.mbChange = true; }
      psReqData = { "ScreenName": "Update Finished Good Costs", "LookUpKey": "Finished Good Key To", "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    } else if (psLookUpKey == "Finished Good Location From") {
      psReqData = { "ScreenName": "Update Finished Good Costs", "LookUpKey": "Finished Good Location From", "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    } else if (psLookUpKey == "Finished Good Location To") {
      psReqData = { "ScreenName": "Update Finished Good Costs", "LookUpKey": "Finished Good Location To", "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    };

    this.objLookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      let obj1: any = {};
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        let psLookupHeading

        if (psLookUpKey == "Finished Good Key From") {
          psLookupHeading = "Finished Good Key From Lookup"
        } else if (psLookUpKey == "Finished Good Key To") {
          psLookupHeading = "Finished Good Key To Lookup"
        } else if (psLookUpKey == "Finished Good Location From") {
          psLookupHeading = "Finished Good Location From Lookup"
        } else if (psLookUpKey == "Finished Good Location To") {
          psLookupHeading = "Finished Good Location To Lookup"
        };

        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psLookupHeading;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (String(obj[pobjFirstInput]).toUpperCase() == String(psValue).toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    })
  }

  bmlfSaveLookupField(pobjEvent) {
    
    this.mbIsLookupModal = false;

    if (pobjEvent.field == "Finished Good Key From Lookup") {
      this.mObjModel.FGKeyFrom = pobjEvent.valueKey.trim();
      this.msFGKeyFromDescription = pobjEvent.value["Desc1"];
    } else if (pobjEvent.field == "Finished Good Key To Lookup") {
      this.mObjModel.FGKeyTo = pobjEvent.valueKey.trim();
      this.msFGKeyToDescription = pobjEvent.value["Desc1"];
    } else if (pobjEvent.field == "Finished Good Location From Lookup") {
      this.mObjModel.FGLocFrom = pobjEvent.valueKey.trim();
      this.msFGLocFromDescription = pobjEvent.value["Description"];
    } else if (pobjEvent.field == "Finished Good Location To Lookup") {
      this.mObjModel.FGLocTo = pobjEvent.valueKey.trim();
      this.msFGLocToDescription = pobjEvent.value["Description"];
    };

    return;

  }


