  public bmlfOnFormFieldChange(mskeyname: string) {

    if (mskeyname == 'FGKeyFrom') {
      this.msFGKeyFromDescription = "";
    } else if (mskeyname == 'FGKeyTo') {
      this.msFGKeyToDescription = "";
    } else if (mskeyname == 'FGLocFrom') {
      this.msFGLocFromDescription = "";
    } else if (mskeyname == 'FGLocTo') {
      this.msFGLocToDescription = "";
    }

  }



  public bmlfChanges_ModeFinal(event) {

    if (event.currentTarget.id = "optFinalMode") {
      if (event.currentTarget.checked) {
        this.mObjModel.UpdateMode = "FINAL";
      } else {
        this.mObjModel.UpdateMode = "TRIAL";
      }
    }

  }

  bmlfonDialogTrial(action) {
    
    if (action == 'Cancel') {
      this.mbShowDialogTrial = false;
    }
    else if (action == 'Ok') {
      this.mbShowDialogTrial = false;
      this.bmlfonprocessaftertrialandfinal()
    }
  }

  bmlfonDialogFinal(action) {
    
    if (action == 'No') {
      this.mbShowDialogFinal = false;
    }
    else if (action == 'Yes') {
      this.mbShowDialogFinal = false;
      this.bmlfonprocessaftertrialandfinal()
    }
  }

  bmlfonProcess() {
    debugger
    // Check permission and return
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (this.mObjModel.FGKeyFrom > this.mObjModel.FGKeyTo) {
      if (this.mObjModel.FGKeyTo.trim() != "") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('UPDATEFINISHEDGOODCOSTS.FinishedGoodKeyLessthan')));
        return false;
      }
    }

    if (this.mObjModel.FGLocFrom > this.mObjModel.FGLocTo) {
      if (this.mObjModel.FGLocTo.trim() != "") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('UPDATEFINISHEDGOODCOSTS.FinishedGoodLocationLessthan')));
        return false;
      }
    }

    if (this.mObjModel.UpdateAveCost == false && this.mObjModel.UpdateStdCost == false && this.mObjModel.UpdateLstCost == false && this.mObjModel.UpdateBasePrice == false && this.mObjModel.UpdateMarketCost == false && this.mObjModel.UpdateMode == 'FINAL') {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('INTERMEDIATECOSTROLLUP.NoCostAreSelectedToUpdate')));
      return false;
    }

    if (this.mObjModel.UpdateMode == "TRIAL") {
      this.mbShowDialogTrial = true;
    } else if (this.mObjModel.UpdateMode == "FINAL") {
      if (this.mObjModel.UpdateStdCost == true) {
        this.mbShowDialogFinal = true
      } else {
        this.bmlfonprocessaftertrialandfinal()
      }
    }
  }

  bmlfonprocessaftertrialandfinal() {
    let InputData = [];
    //InputData.push(this.mobjIPModel);
    InputData.push({
      FGKeyFrom: this.mObjModel.FGKeyFrom,
      FGKeyTo: this.mObjModel.FGKeyTo,
      FGLocFrom: this.mObjModel.FGLocFrom,
      FGLocTo: this.mObjModel.FGLocTo,
      IncludeLaborAndOverhead: this.mObjModel.IncludeLaborAndOverhead,
      UpdateStdCost: this.mObjModel.UpdateStdCost,
      UpdateAveCost: this.mObjModel.UpdateAveCost,
      UpdateLstCost: this.mObjModel.UpdateLstCost,
      UpdateBasePrice: this.mObjModel.UpdateBasePrice,
      UpdateMarketCost: this.mObjModel.UpdateMarketCost,
      UpdateMode: this.mObjModel.UpdateMode,
      FMWeight: this.mObjModel.FMWeight

    });

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: InputData,
        Response: [{
          "ResponseText": "", "ErrorCode": "", "Pdfpath": ""
        }]
      })
    };
    this.mbloaderEnabled = true;
    let pDefaultReportType = localStorage.getItem('reportType');

    this.objService.bmlfonProcessData(reqData).subscribe(res => {
      debugger
      if (res == null) {
        
        this.objToastService.notifyError('Server Error');
        this.mbloaderEnabled = false;
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.mbloaderEnabled = false;
        // this.pdfSrc = res.Response[0].Pdfpath
        // this.modalservice.open('pdfViewerModal');
        if (res.Response[0].ResponseText != "") {
          this.msMsgData.screenName = this.msScreenname;
          this.msMsgData.Data = res.Response[0].ResponseText;
          this.mbReportDialog = true
          // this.bmlfReportModal('open');

        }
        this.pdfSrc = res.Response[0].Pdfpath
        if (pDefaultReportType == 'Print') {
          this.objUtils.bmgfPrintPDF(this.pdfSrc)
          // this.bmgfPrintPDF(pPDFSrc);
        }
        else if (pDefaultReportType == 'NewTab') {
          this.objUtils.bmgfOpenPDFNewTab(this.pdfSrc);
        } else {
          this.objUtils.bmgfDownloadFile(this.pdfSrc, this.msScreenname, pDefaultReportType);
        }
        // this.modalservice.open('pdfViewerModal');

        return false;
      }
      else {
        if (res.Response[0].ResponseText == '') {
          this.objToastService.notifyError('Error in service.')
        }
        else {
          if (res.Response[0].ResponseText.length > 25) {
            //this.objToastService.notifyError(res.Response[0].ResponseText)
            this.msReport = res.Response[0].ResponseText;
            this.bmlfReportModal('open');
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText)
          }
        }
        this.mbloaderEnabled = false;
      }

      console.log(this.gridSettings)
      this.mbloaderEnabled = false;
    });
  }

