  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.objUtils.bmgfonTranslate('COMMON.MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.objUtils.bmgfonTranslate('COMMON.TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenname;
      pobjFormRec.primeKey = this.mObjModel.FMWeight;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

