import { Component, OnInit, Renderer2 } from '@angular/core';
import { LookupKey, ACTION, RESPONSE_MSG, SQL, NAVIGATE_URL, ProductCostConstants } from 'src/app/main/costing/constant/product-cost-analysis.const';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { Router } from '@angular/router';
import { FormulaEntryService } from 'src/app/main/r-and-d/http/formula-entry.service';
import { AlertService } from 'src/app/core/services/alert.service';
import * as moment from 'moment';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { SelectableSettings, SelectAllCheckboxState, PageChangeEvent, DataStateChangeEvent, GridComponent }
  from '@progress/kendo-angular-grid';
import { DataBindingDirective } from '@progress/kendo-angular-grid';
import { ProductCostAnalysisService } from 'src/app/main/costing/http/product-cost-analysis.service';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { ModalService } from 'src/app/core/services/modal.service';
import { FMMast } from '../../../../models/product-cost.model';

@Component({
  selector: 'app-product-cost-analysis-list',
  templateUrl: './product-cost-analysis-list.component.html',
  styleUrls: ['./product-cost-analysis-list.component.scss']
})
export class ProductCostAnalysisListComponent extends BaseGrid implements OnInit {
  public bmlfSelectedCallback = (args) => args.dataItem;
  public gridDataList = [];
  public gridView = [];
  public orginalGridList = [];
  public mbloadingEnabled = false;
  public gridSettings: GridSettings = new GridSettings();
  public mscustomDateFormat: string = "";
  public mbShowDeleteDialog: boolean = false;
  public mObjModel: FMMast = new FMMast();
  public msPaymentKey: string = "";
  public maarraySelected: Array<any> = [];
  //public tmpmaarraySelected: Array<any> = [];
  public mbBulkDelete: boolean = false;
  public msScreenName: string = 'Product Cost Analysis';
  public msMsgData: any = {};
  public mbBulkDeleteView: boolean = false;
  public msActiveFilter: string = "";
  public mbUnPosted: boolean = true;
  public msReportDtl: string = "";
  public mbReportDialog: boolean = false;
  public msActive: string = "Active";
  public msApproved: string = "Approved";
  public msDevelopment: string = "Development";
  public msPending: string = "Pending";
  public mbClearFilter: boolean = false; 
  public msFormulaID: string = '';
  public msRevisionNumber: string = '';
  public msFormulaStatus: string = '';
  public mbBulkPrint : boolean = false;
  constructor(private router: Router,
    public renderer: Renderer2,
    private lookupService: LookupService,
    private lsService: LocalStorageService,
    private objService: ProductCostAnalysisService,
    public dateFormatService: DateFormatService, public objToastService: ToastMessageService,
    public objUtil: AppUtility,
    public userFieldService: UserFieldService) {
    super(lsService, dateFormatService, userFieldService, objToastService, objUtil);
  }

  ngOnInit() {
    
    this.mscustomDateFormat = this.dateFormatService.getDateFormat();
    this.initGrid();
    this.objUtil.getBMEScreePermission(this.msScreenName);
    this.getDataList();
    this.setExportData({ 'pageName': this.msScreenName });
  }

  getDataList(): void {
    debugger
    this.mbloadingEnabled = true;
    let pobjReqData: any = {
      ScreenName: LookupKey.ScreenName ,
      LookUpKey: "ProductCostAnalysisList",
      Filter1: JSON.parse(localStorage.usersession).UserId
    }
    this.lookupService.getLookup(pobjReqData).subscribe(res => {
      if (res == null) { return false; }
      this.gridDataList = [];
      this.maarraySelected =[];
      this.mbloadingEnabled = false;

      let parrGridData = res.LookupData;
      let dtFormat = this.mscustomDateFormat + " hh:mm";
      parrGridData.forEach(ele => {
        ele.RevisionDate =  moment(new Date(ele.RevisionDate)).format(this.mscustomDateFormat.toUpperCase() + ' HH:mm');      
        ele.RecDate = moment(new Date(ele.RecDate)).format(this.mscustomDateFormat.toUpperCase());
      })
      this.bmlfGetFiltersCounts(parrGridData);
      let parrList;
      if (this.msActiveFilter != '') {
        parrList = parrGridData.filter(x => x['RevisionStatus'] == this.msActiveFilter);
      } else {
        parrList = parrGridData;
      }
       
      this.gridDataList = parrList;
      this.orginalGridList = parrList;
      this.gridView = this.gridDataList;
      let parrSettingData = res.GridSettingData[0].JSONString;
      this.setInitGridSettings(parrSettingData, "PRODUCT_COST");
      this.mobjGridSettings.state.take=50;
     
    }
      , error => {
        console.log(error);
      }

    ).add(() => { this.mbloadingEnabled = false })

  }

  bmlfGetFiltersCounts(gridData) {
    let parrPendingList = gridData.filter(x => x['RevisionStatus'] == 'PENDING');
    this.msPending = "Pending (" + String(parrPendingList.length) + ")";
    let parrActiveList = gridData.filter(x => x['RevisionStatus'] == 'ACTIVE');
    this.msActive = "Active (" + String(parrActiveList.length) + ")";
    let parrDevlopmentList = gridData.filter(x => x['RevisionStatus'] == 'DEVELOPMENT');
    this.msDevelopment = "Development (" + String(parrDevlopmentList.length) + ")";
    let parrApprovedList = gridData.filter(x => x['RevisionStatus'] == 'APPROVED');
    this.msApproved = "Approved (" + String(parrApprovedList.length) + ")";
  }

  //---------------------------------------------------------------------------------------------
  bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings(this.msScreenName, "PRODUCT_COST");
  }
  //---------------------------------------------------------------------------------------------    
  bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid(this.msScreenName);
  }
  //---------------------------------------------------------------------------------------------
  //Add
  bmlfonAddEntry(): void {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate(['/main/costing/product-cost-analysis/manage']);
  }
  //---------------------------------------------------------------------------------------------
  //Edit
  bmlfonEdit(dataitem) {
    this.router.navigate(['/main/costing/product-cost-analysis/manage'], { queryParams: { FormulaID: dataitem.FormulaID, RevisionNumber: dataitem.RevisionNumber } });
  }
  //---------------------------------------------------------------------------------------------
  //copy
  bmlfonCopy(dataitem) {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate(['/main/costing/product-cost-analysis/manage'], { queryParams: { FormulaID: dataitem.FormulaID, RevisionNumber: dataitem.RevisionNumber, COPY: "YES" }, skipLocationChange: true });
  }
  //---------------------------------------------------------------------------------------------
  //Delete record
  bmlfonDelete(dataitem) {
    // Only Status : New record can delete
    if (dataitem['RevisionStatus'].toUpperCase() == 'ACTIVE') {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('PRODUCT_COST.VALID.DELACTIVE'));
      return;
    }
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };
    this.msFormulaID = dataitem['FormulaID'];
    this.msRevisionNumber = dataitem['RevisionNumber'];
    this.msFormulaStatus = dataitem['RevisionStatus'];
    this.mbShowDeleteDialog = true;
  }
  //---------------------------------------------------------------------------------------------
  //Bulk Delete
  onActionDelete() {
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('COMMON.MSG.NODATAFORDELETE'))
      return;
    }
    this.mbBulkDelete = true;
    this.mbShowDeleteDialog = true;
  }
  //---------------------------------------------------------------------------------------------
  bmlfonfilterEvent(obj) {
    if (obj.Type == "String") {
      let filterGrid = this.orginalGridList.filter(x => x[obj.FirstKey] == obj.FirstValue);
      this.gridDataList = filterGrid;
    }
    else if (obj.Type == "Date") {
      let filterGrid = this.orginalGridList.filter(x => (new Date(x[obj.FirstKey]).getTime() >= obj.FirstValue.getTime()) && new Date(x[obj.FirstKey]).getTime() <= obj.SecondValue.getTime());
      this.gridDataList = filterGrid;
    }
  }
  //---------------------------------------------------------------------------------------------
  bmlfonDialogAction(action) {
    debugger
    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {

      if (this.mbBulkDelete == true) {
        let psLength: any = 0;
        psLength = this.maarraySelected.length;
    
        let CustomerObj: any = {
          HeaderData: [],
          Response: [{ ResponseText: '', 'ErrorCode': '' }]
        }
    
        let Keys: any = {};
        let marrLineGridData = [];
        Keys.FormulaID = '';
        for (let i = 0; i <= psLength - 1; i++) {         
          Keys = {
            FormulaID: this.maarraySelected[i]["FormulaID"],
            RevisionNumber: this.maarraySelected[i]['RevisionNumber'],
            FMstate: this.maarraySelected[i]['RevisionStatus'] // 21/Sep/2020 
          };
          CustomerObj.HeaderData.push(Keys);
        }
    
        let reqData: any = {
          JSON: JSON.stringify(CustomerObj)
        };

        this.objService.BMGFDeleteCostAnalysis(reqData).subscribe(res => {
          if (res == null) {
            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {
            {
              //this.objToastService.notifySuccess(res.Response[0].ResponseText);
              this.msMsgData.Data = res.Response[0].ResponseText;            
              this.msMsgData.Header = "BatchMaster";
              this.msMsgData.mbBulkDeleteView = true;
              this.mbBulkDeleteView = true;

              this.mbShowDeleteDialog = false;
              this.mbBulkDelete = false;
              this.maarraySelected = [];
              this.getDataList();
            }
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText)
            this.mbShowDeleteDialog = false;
            this.maarraySelected = [];
            this.getDataList();
          }

        })
      }
      else {      
        let reqData: any = {
          JSON: JSON.stringify({
            HeaderData: [{ FormulaID: this.msFormulaID, RevisionNumber: this.msRevisionNumber, FMstate: this.msFormulaStatus }],
            Response: [{ ResponseText: "", ErrorCode: "" }]    
          })
        };
        this.objService.BMGFDeleteCostAnalysis(reqData).subscribe(res => {
          if (res == null) {
            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {
            {
              if (res.Response[0].ResponseText.search("Reference") != -1) {
                this.objToastService.notifyError(this.objUtil.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
                this.mbShowDeleteDialog = false;
                return;
              }
              else if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
                this.objToastService.notifySuccess((this.objUtil.bmgfonTranslate('COMMON.MSG.DELETE')));
                this.mbShowDeleteDialog = false;
                this.mbBulkDelete = false;
                this.maarraySelected = [];
                this.getDataList();
              } else {
                this.objToastService.notifyError(res.Response[0].ResponseText);
                this.maarraySelected = [];
                this.mbShowDeleteDialog = false;
              }
            }
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText);
            this.maarraySelected = [];
          }
        })
      }
    }
  }
  //-----------------------------------------------------------------------------------------
  bmlfonselectionChange(e) {

    if (e.selectedRows.length > 0) {
      e.selectedRows.forEach(ele => {
        let parrfilterData = this.maarraySelected.filter(x => x.FormulaID == ele.dataItem.FormulaID);
        if (parrfilterData == undefined || parrfilterData.length == 0) {
          this.maarraySelected.push(ele.dataItem);
        }
      })
    }
    else if (e.deselectedRows.length > 0) {
      e.deselectedRows.forEach(ele => {
        let pobjTempData = ele.dataItem;
        this.maarraySelected = this.maarraySelected.filter(x => {
          if (x.FormulaID != pobjTempData.FormulaID) {
            return x;
          }
        })
      })
    }
  }
  //--------------------------------------------------------------------------------------------
  public bmlfoncloseModal(event) {
    this.mbBulkDeleteView = event.mbBulkDeleteView;
  }
  //--------------------------------------------------------------------------------------------
  pageChange(e) {
    this.maarraySelected = [];
  }
  //--------------------------------------------------------------------------------------------
  bmlFilterSetting() {
    this.filterEnabled = !this.filterEnabled;
  }

  bmlfGetEntry(EntryType) {
    debugger
    this.mbClearFilter = true;
    this.msActiveFilter = EntryType;      
    this.getDataList();
    if (EntryType == "ACTIVE") {
      this.mbClearFilter = false;
    }
  }

  bmlfReportModal(psKey) {
    debugger
    if (psKey == 'open') {
      this.mbReportDialog = true;
    } else if (psKey == 'close' || psKey == 'ok') {
      this.mbReportDialog = false;
    }
  }
  
  //print 
bmlfonPrint(dataitem:any) {
 try{
   let printReqParam:any = {
     "ReportName":"Product Cost Analysis Report",
     "FormulaKeyFrom":this.mbBulkPrint ? '' : dataitem.FormulaID,
     "FormulaKeyTo":this.mbBulkPrint ? '' : dataitem.FormulaID,
     "FormulaDescriptionFrom": '',
     "FormulaDescriptionTo": "",
     "FormulaClassFrom": "",
     "FormulaClassTo" : '',
     "FormulaStatus":  '',
     "CostMethod": "Standard",
     "CostbyWeight/Volume" : "Weight",
     "PrintMarkup/Margin" : "Markup",
     "ProductAssemblyBOM/BOMKey#1": "",
     "ProductAssemblyBOM/BOMKey#2": "",
     "ProductAssemblyBOM/BOMKey#3": "",
     "ListingFlag": true,
     "ListingVariable": this.mbBulkPrint ? dataitem : dataitem.FormulaID,
     "Parameter":""
   }
   this.objUtil.bmgfReportAction(printReqParam,'Product Cost Analysis Report').then((value) => {
     this.mbBulkPrint = false;
     this.getDataList();
   });

 }catch(e){
   console.log(e);
 }
}

onActionPrint(){
  this.mbBulkPrint = true;
  let psFormulaID = this.maarraySelected.map(res =>{
    return res.FormulaID
  });
  var psStringFormulaID = psFormulaID.join("").toString();
  this.bmlfonPrint(psStringFormulaID)
 }
 
}
