import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { ProductCostAnalysisListComponent } from 'src/app/main/costing/components/Costing/Product-Cost-Analysis/product-cost-analysis-list/product-cost-analysis-list.component';
import { ProductCostAnalysisManageComponent } from 'src/app/main/costing/components/Costing/Product-Cost-Analysis/product-cost-analysis-manage/product-cost-analysis-manage.component';
import { CostingComponent } from 'src/app/main/costing/costing.component';
import { IntermediateCostRollupComponent } from './components/intermediatecostrollup/intermediatecostrollup.component';
import { UpdateFinishedGoodCostsComponent } from './components/updatefinishedgoodcosts/updatefinishedgoodcosts.component';
import { FormulaLossAdjustmentComponent } from './components/formulalossadjustment/formulalossadjustment.component';
import { CostingSetupComponent } from './components/Costing/costing-setup/costing-setup.component';

const routes: Routes = [
    {
        path: '',
        component: CostingComponent ,
        children:
            [
                {
                    path: '', redirectTo:  'costing', pathMatch: 'full'
                }, {
                    path: 'costing', component: CostingComponent, canActivate: [AuthGuard]
                }, {
                    path: 'product-cost-analysis', component: ProductCostAnalysisListComponent, canActivate: [AuthGuard]
                }, {
                    path: 'product-cost-analysis/manage', component: ProductCostAnalysisManageComponent, canActivate: [AuthGuard]
                }, {
                    path: 'intermediatecostrollup', component: IntermediateCostRollupComponent, canActivate: [AuthGuard]
                }, {
                    path: 'updatefinishedgoodcosts', component: UpdateFinishedGoodCostsComponent, canActivate: [AuthGuard]
                }, {
                    path: 'formulalossadjustment', component: FormulaLossAdjustmentComponent, canActivate: [AuthGuard]
                }, {
                    path: 'costing-setup', component: CostingSetupComponent, canActivate: [AuthGuard]
                }
            ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class CostingRoutingModule { }




